* {
  outline: none;  //transition: ease-out .2s;    /* remove all animation */  /*-webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  transition: none !important;*/
}

html {
  width: 100%;
  height: 100%;
  //background-color: $color-2nd;
}

// Font smoothing hack
html, html a {
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}

body {
  overflow-x: hidden;
  letter-spacing: 0.2px;
  font-style: normal;
  width: 100vw;
  min-height: 100vh;
  position: relative;
  background: var(--bs-body-bg)!important;
  line-height: $line-height;
  padding: 0 !important;
  margin: 0 !important;
  font-family: $font;
  font-size: $font-size;  font-weight: $normal;
  color: $text-color;  display: block;  /*display: flex;  flex-direction: column;  justify-content: space-around;  gap: 0;*/    * {  	transition: ease-out .2s;  }
}h1, h2, h3, h4, h5, h6 {	font-family: $heading-font;	line-height: $line-height;}

input, textarea, button, a, p, span {
  font-family: $font;
}

input, textarea, button, select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

textarea {
  min-height: 50px;
  resize: vertical;
}

a, a:hover, a:focus, a:active {
  color: $text-color;
  text-decoration: none !important;
  cursor: pointer;
}

p {
  color: $text-color;
  font-size: $font-size;
  line-height: $line-height;
}

/*strong {
  font-weight: 600;
}*/

*:focus, :active {
  outline: none !important;
  box-shadow: none;
}
