/* Home */.home-wonder-popup {	position: relative;	margin-top: -6rem;	//padding: 1rem 0 2rem;		@include media-breakpoint-down(lg) {		margin-top: 0;		padding-top: 0;		padding-bottom: 1rem;		overflow: visible;				.container {			background: $color-2nd;						@include media-breakpoint-only(sm) {				max-width: 100%;			}		}	}		.heading {		background: $color-2nd;		padding: 3rem 4rem;		width: 48vw;		max-width: 750px;		margin: 0 auto;		position: relative;				@include media-breakpoint-down(lg) {			width: 100%;			background: transparent;			padding-left: 0;			padding-right: 0;		}				&:before {			content: '';			position: absolute;			width: 30%;			background: $color-main;						@include media-breakpoint-up(lg) {				bottom: -.93755rem;				height: 1.875rem;			}			@include media-breakpoint-down(lg) {				top: -.75rem;				height: 1.25rem;			}		}				.subtitle {			font-size: 1.25rem;			color: white;		}		.title {			font-size: 3rem;			color: $color-main;		}		.tool-editor {			margin: 0;			color: white!important;			* {				color: inherit!important;			}		}	}}.home-wonder-list {		@include media-breakpoint-down(lg) {		padding-top: 0;	}		.slider {		.slick-arrow {			&.btn-prev {				left: 0;								@include media-breakpoint-only(lg) {					left: -2rem;				}			}			&.btn-next {				right: 0;								@include media-breakpoint-only(lg) {					right: -2rem;				}			}		}		.card-large {			padding: 0 1px;		}	}		.card-large {		.card-img-top {			height: 400px;		}	}}.home-recipes {	background: $color-2nd;		.heading {		display: flex;		flex-direction: row;		justify-content: center;				.title {			text-align: center;			}	}		.home-recipes-list {		padding: 2rem 0;				@include media-breakpoint-up(lg) {			gap: 1rem;		}		@include media-breakpoint-down(lg) {				display: block;						&.slider {				.slick-dots {					li button {						background: white;					}					li.slick-active button {						background: $color-main;					}				}				.slick-arrow {					&.btn-prev {						left: -.5rem;					}					&.btn-next {						right: -.5rem;					}				}			}						.recipe-card.feature {				flex-basis: 100%	;			}		}	}		.btn, .btn:visited {		color: $color-main;				&:hover, &:active {			color: white;			box-shadow: none;		}	}}.home-benefit {	overflow: visible;	position: relative;		@include media-breakpoint-up(lg) {		min-height: 800px;
	}
	@include media-breakpoint-down(lg) {		padding-bottom: 6rem;
	}		&::before {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;		background: $color-2nd;
		background: linear-gradient(180deg, rgba( $color-main, 0) 50%, rgba( #170D14, .8) 100%);		z-index: 1;	}		.heading {		color: $color-2nd;				@include media-breakpoint-up(xl) {			padding-bottom: 3rem;
		}
		@include media-breakpoint-down(xl) {			padding-bottom: 1rem;
		}				> * {			color: inherit;		}	}		.benefit-img {		position: absolute;				left: 50%;		transform: translateX(-50%);				z-index: 1;				@include media-breakpoint-up(lg) {			bottom: -9rem;			width: 400px;
		}
		@include media-breakpoint-down(lg) {			bottom: -6rem;			width: 60vw;
		}	}	.btn {		position: relative;		z-index: 2;				&:hover {			color: $color-2nd;			box-shadow: none;		}	}		.list-benefit {		position: relative;		z-index: 2;				@include media-breakpoint-up(lg) {			padding: 3rem;			gap: 3rem;		}		@include media-breakpoint-down(lg) {				display: block;			padding: 0 1rem 3rem;						&.slider {				.slick-arrow {					i {						color: white;					}					&.btn-prev {						left: -.25rem;					}					&.btn-next {						right: -.25rem;					}				}				.slick-dots {					li button {						background: rgba(255, 255, 255, .35);					}					li.slick-active button {						background: $color-2nd;					}				}				}		}					.item {			color: white;			display: flex;			align-items: center;						@include media-breakpoint-up(lg) {				flex-direction: row;				flex-basis: 30%;				gap: 2rem;
			}
			@include media-breakpoint-down(lg) {				flex-direction: column;				justify-content: center;				gap: 1rem;
			}						img {								aspect-ratio: 1/1;								@include media-breakpoint-up(lg) {					height: 6rem;					order: 1;
				}
				@include media-breakpoint-down(lg) {					width: 9rem;					order: 0;
				}			}			.desc {				flex-basis: 90%;				text-align: right;				display: flex;				flex-direction: column;				gap: .25rem;								@include media-breakpoint-up(lg) {					order: 0;
				}
				@include media-breakpoint-down(lg) {					order: 1;
				}								.title {					color: inherit;					text-transform: uppercase;					font-weight: 800;					font-size: 1.125rem;					
					@include media-breakpoint-down(lg) {						text-align: center;
					}				}				.tool-editor {					color: inherit!important;					
					@include media-breakpoint-down(lg) {						text-align: center;
					}								> p {						margin: .25rem 0;						text-align: inherit!important;					}				}			}		}	}}.home-news {	padding-top: 9rem;		@include media-breakpoint-down(lg) {			padding-top: 7rem;	}		.heading {		.tool-editor {			margin: 2rem 0;						@include media-breakpoint-down(lg) {					margin: 1rem 0 0;			}		}	}		.list-news {		padding-bottom: 2rem;				@include media-breakpoint-up(lg) {			justify-content: space-around;		}		@include media-breakpoint-down(lg) {				display: block;			padding: 0 .25rem 3rem;						&.slider {				.slick-arrow {					&.btn-prev {						left: -.25rem;					}					&.btn-next {						right: -.25rem;					}				}			}		}				.card {			flex-basis: 30.5%;		}	}}.home-grow {	@include media-breakpoint-up(lg) {		background: white;
	}
	@include media-breakpoint-down(lg) {		background: $color-3rd;		padding-bottom: 0;
	}				.heading {		.tool-editor {			margin: 2rem 0;						@include media-breakpoint-down(lg) {					margin: 1rem 0 0;			}		}	}	.list-grow {		justify-content: center;		flex-wrap: nowrap;		gap: 1rem;				@include media-breakpoint-down(lg) {				flex-direction: column;			margin-bottom: .75rem;		}				.card {			flex-basis: 50%;			position: relative;						* {				transition: ease-out .2s;			}						.card-img-top {				aspect-ratio: 5/4;			}						.card-body {				position: absolute;				bottom: 0;				left: 0;				width: 100%;				background: linear-gradient(180deg, rgba( $color-main, 0) 0%, rgba( #170D14, .8) 100%);				display: flex;				justify-content: flex-end;								.card-text{					display: none;				}								.card-title {					color: white;					font-weight: 800;				}			}						@include media-breakpoint-up(lg) {				&:hover {					.card-body {						height: 100%;						background: linear-gradient(180deg, rgba( $color-main, .75) 0%, rgba( #170D14, .95) 100%);												.card-title {							color: $color-2nd;						}						.card-text{							display: block;							color: white;							opacity: .75;						}					}				}			}		}	}}.home-partner {	display: flex;	flex-direction: row;	gap: 0;	align-items: stretch;		@include media-breakpoint-down(lg) {		padding: 0;		flex-direction: column;	}			.partner-img {		order: 1;		object-fit: cover;				@include media-breakpoint-up(lg) {			aspect-ratio: 5/4;			flex-basis: 50%;			width: 50vw;
		}
		@include media-breakpoint-down(lg) {			aspect-ratio: 1/1;			width: 100%;
		}	}	.partner-body {		order: 0;		background: $color-main;		display: flex;		flex-direction: column;		gap: 1rem;		align-items: center;		justify-content: center;				@include media-breakpoint-up(lg) {			flex-basis: 50%;			padding: 10rem;
		}
		@include media-breakpoint-down(lg) {			width: 100%;			padding: 4rem 1rem;
		}				.heading {			.tool-editor {				margin-top: 1rem;				color: white;								> * {					color: inherit!important;				}			}			.title {				color: $color-2nd;			}		}	}	.btn {		border-color: $color-2nd;		color: $color-2nd;				&:hover {			background: $color-2nd;			color: $color-main;		}	}}.home-contact {	padding-bottom: 0;	position: relative;	display: flex;	flex-direction: column;	gap: 0;		.heading .tool-editor {		margin-bottom: 1rem;	}	.contact-prune {		position: absolute;		z-index: 1;				@include media-breakpoint-up(xl) {			top: 0;			left: 0;			width: 25vw;
		}
		@include media-breakpoint-down(xl) {			width: 40vw;			bottom: 40vw;			left: 50%;			transform: translate(-50%);
		}	}	.contact-img {		width: 100vw;				@include media-breakpoint-up(xl) {
		}
		@include media-breakpoint-down(xl) {			object-fit: cover;			height: 100vw;
		}	}}