@use 'sass:map';@use 'sass:list';@use "sass:meta";@use 'sass:color';@mixin color-scheme($name) {
  @media (prefers-color-scheme: #{$name}) {
    @content;
  }
}@mixin color-mode($mode: light, $root: false) {
  @if $color-mode-type == "media-query" {
    @if $root == true {
      @media (prefers-color-scheme: $mode) {
        :root {
          @content;
        }
      }
    } @else {
      @media (prefers-color-scheme: $mode) {
        @content;
      }
    }
  } @else {
    [data-bs-theme="#{$mode}"] {
      @content;
    }
  }
}// calculate
@mixin calc($property, $expression) {
  #{$property}: -moz-calc(#{$expression}) !important;
  #{$property}: -webkit-calc(#{$expression}) !important;
  #{$property}: calc(#{$expression}) !important;
}@function css-function( $function, $values... ) {
	@return 
		$function
		+ unquote( '(' )
		+ $values
		+ unquote( ')' )
	;
}@function css-min( $values... ) {
	@return css-function( min, $values );
}

@function css-max( $values... ) {
	@return css-function( max, $values );
}

@function css-clamp( $values... ) {
	@return css-function( clamp, $values );
}

// rotate
@mixin rotate($deg) {
  @include transform(rotate(#{$deg}deg));
}

@mixin scaleX($val) {
  @include transform(scaleX($val));
}

// generic transform
@mixin transform($transforms) {
  -moz-transform: $transforms;
  -o-transform: $transforms;
  -ms-transform: $transforms;
  -webkit-transform: $transforms;
  transform: $transforms;
}

// translate
@mixin translate($x, $y) {
  @include transform(translate($x, $y));
}

// skew
@mixin skew($x, $y) {
  @include transform(skew(#{$x}deg, #{$y}deg));
}

@mixin skew3D($x, $y, $z) {
  @include transform(skew(#{$x}deg, #{$y}deg, #{$z}deg));
}

//transform origin
@mixin transform-origin($origin) {
  moz-transform-origin: $origin;
  -o-transform-origin: $origin;
  -ms-transform-origin: $origin;
  -webkit-transform-origin: $origin;
  transform-origin: $origin;
}

@mixin linear-gradient($top, $bottom) {
  background: $top; /* Old browsers */
  background: -moz-linear-gradient(top, $top 0%, $bottom 100%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, $top), color-stop(100%, $bottom)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, $top 0%, $bottom 100%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top, $top 0%, $bottom 100%); /* Opera 11.10+ */
  background: -ms-linear-gradient(top, $top 0%, $bottom 100%); /* IE10+ */
  background: linear-gradient(to bottom, $top 0%, $bottom 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#000000', GradientType=0); /* IE6-9 */
}

@mixin horizontal-gradient($from, $to, $stop: 100%) {
  /* fallback/image non-cover color */
  background-color: $from;

  /* Firefox 3.6+ */
  background-image: -moz-linear-gradient(left, $from 0%, $to $stop);

  /* Safari 4+, Chrome 1+ */
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0%, $from), color-stop($stop, $to));

  /* Safari 5.1+, Chrome 10+ */
  background-image: -webkit-linear-gradient(left, $from 0%, $to $stop);

  /* Opera 11.10+ */
  background-image: -o-linear-gradient(left, $from 0%, $to $stop);

  /* IE10+ */
  background: -ms-linear-gradient(left, $from 0%, $to $stop);

  /* Standard */
  background: linear-gradient(to right, $from 0%, $to $stop);

  /* IE6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$from', endColorstr='$to', GradientType=1);
}@mixin keyframes($name) {
  @-webkit-keyframes #{ $name } { @content; }
  @-moz-keyframes #{ $name } { @content; }
  @-ms-keyframes #{ $name } { @content; }
  @keyframes #{ $name } { @content; }
}
@mixin setKeyFrame($name, $args) {	//create keyframe first	@include keyframes($name) {		//then @content		$keyStops: map-keys($args);//key lv1				@for $i from 0 to length($keyStops) {			$key: nth($keyStops, $i + 1);//get values of key lv1			//@debug "$keyLv1:" + $key;						#{ $key } {				$innermap: map-get($args, $key);//get map in key lv1				$innerKeyStop: map-keys($innermap);//get keys lv2 in map with key lv1								@for $j from 0 to length($innerKeyStop) {					$innerkey : nth($innerKeyStop, $j + 1);//get values of key lv2					//@debug "$keyLv2" + nth($innerKeyStop, $j + 1);					//@debug map-get($innermap, $innerkey);										#{ $innerkey } : map-get($innermap, $innerkey);				}			}		}	}}@mixin animation-mixin($name, $color) {	@keyframes #{$name} {		0% {
	        box-shadow:0 0 8px 6px rgba($color, 0), 0 0 0 0 rgba($color, 0), 0 0 0 0 rgba($color, 0)
	    }
	    10% {
	        box-shadow:0 0 8px 6px rgba($color, .5), 0 0 12px 10px rgba($color, 0), 0 0 12px 14px rgba($color, .5)
	    }
	    100% {
	        box-shadow:0 0 8px 6px rgba($color, 0), 0 0 0 40px rgba($color, 0), 0 0 0 40px rgba($color, 0)
	    }	}}