	 height: 100vh;
	}
	
	.image-grid {
	 display: grid;
	 grid-template-columns: repeat(12, 1fr);
	 grid-column-gap: 1rem;
	 grid-row-gap: 33.3vh;
	 width: 100vw;
	 margin: 0 auto;
	
	 .image_cont {
	  position: relative;
	  overflow: hidden;
	  height: 80vh;
	  max-height: 500px;
	 }
	
	 img {
	  position: absolute;
	  width: 100%;
	  height: 160%;
	  object-fit: cover;
	  bottom: 0;	 	display: none;
	 }
	
	 .image_cont:nth-child(1) {
	  grid-column: 1 / -1;
	  grid-row: 1;
	  height: 95vh;
	 }
	
	 .image_cont:nth-child(2) {
	  grid-column: 2 / span 8;
	  grid-row: 2;
	  height: 60vh;
	 }
	
	 .image_cont:nth-child(3) {
	  grid-column: 4 / span 8;
	  grid-row: 3;
	  height: 60vh;
	  img {
	   object-position: right;
	  }
	 }
	
	 .image_cont:nth-child(4) {
	  grid-column: 1 / -1;
	  grid-row: 4;
	  height: 60vh;
	 }
	
	 .image_cont:nth-child(5) {
	  grid-column: 4 / span 8;
	  grid-row: 5;
	  height: 80vh;
	 }
	
	 .image_cont:nth-child(6) {
	  grid-column: 2 / span 8;
	  grid-row: 6;
	  height: 80vh;
	 }
	
	 @media screen and (min-width: 1000px) {
	  .image_cont {
	   max-height: unset;
	  }
	
	  .image_cont:nth-child(2) {
	   grid-column: 2 / span 4;
	  }
	
	  .image_cont:nth-child(3) {
	   grid-column: 8 / span 4;
	  }
	
	  .image_cont:nth-child(5) {
	   grid-column: 8 / span 4;
	  }
	
	  .image_cont:nth-child(6) {
	   grid-column: 2 / span 4;
	  }
	 }
	}
	
	.overlay {
	 position: fixed;
	 height: 100vh;
	 display: grid;
	 grid-template-columns: repeat(12, 1fr);
	 grid-column-gap: 1rem;
	 grid-row-gap: 10rem;
	 width: 100vw;
	 margin: 0 auto;
	 z-index: -1;
	
	 div {
	  opacity: 0.2;
	  border-left: 0.5px white solid;
	  border-right: 0.5px white solid;
	 }
	}}.parallax06 {	padding: 0!important;	.heading {		top: 5rem;		height: auto;	}		.reflection3D {	  $tvItemWidth: 50vw;	  $tvItemHeight: $tvItemWidth * 9 / 16; //aspect ratio 16/9	  	  $layers:
		  '../img/component/lightGallery/thumb/html5-video-poster.jpg',
		  '../img/component/customAnimation/13/cloud1.png',
		  '../img/component/customAnimation/13/moutain.png',		  '../img/component/customAnimation/13/forest.png',		  '../img/component/customAnimation/13/cloud2.png',		  '../img/component/customAnimation/13/trees.png';	
	  position: relative;
	  height: 100vh;
	  perspective: 30vw;	  	  .ad {
		position: absolute;
		left: 50%;
		top: 50%;
		width: $tvItemWidth;
		height: $tvItemHeight;
		margin-left: -$tvItemWidth / 2;
		margin-top: -$tvItemHeight / 2;
		background: black;
		/*overflow: hidden;*/
		border-radius: 0;
		-webkit-transform-style: preserve-3d;
		will-change: transform;
		box-shadow: 0 3.75rem 6.25rem rgba( black, .5);	  		  	.layer {
			position: absolute;
			will-change: transform;			background-position: center bottom;			background-repeat: no-repeat;			background-size: 100%;			overflow: hidden;		  			&.light {
				top: 50%;
				left: 50%;
				margin-left: -$tvItemWidth;
				margin-top: -$tvItemHeight * 1.6;
				width: $tvItemWidth * 2;
				height: $tvItemHeight * 2;
				background: radial-gradient(ellipse closest-side at center, rgba(white, 1), rgba(black, 1) );
				mix-blend-mode: color-dodge;
				opacity: .45;
				z-index: 3;			}			&.light-2 {
				z-index: 3;
				background: radial-gradient(ellipse closest-side at center, rgba(white, 1), rgba(black, 1) );
				mix-blend-mode: screen;
				opacity: .15;			}
		}				[class*="image-"] {	  		top: 50%;	  		left: 50%;	  		margin-left: -$tvItemWidth / 2;	  		margin-top: -$tvItemHeight / 2;	  		width: $tvItemWidth;	  		height: $tvItemHeight;	  		transform: scale(.7);	  		filter: drop-shadow(-2rem 3rem 3rem rgba( black, .75) );	  			  		@for $i from 1 through length($layers) {
				&:nth-child( #{$i} ) {
					background-image: url( nth($layers, $i) );
					z-index: $i;
				}
			}			/*&:nth-child(n + 1) {				background-repeat: repeat-x;			}*/	  				  	&:nth-child(2) {		  		//background-image: url(../img/component/customAnimation/13/cloud1.png);		  		background-position: center center;		  		background-size: 80%;		  		//background-repeat: repeat;				opacity: .25;		  		//z-index: 2;		  	}		  	/*&:nth-child(3) {		  		background-position: center center;		  	}*/		  	&:nth-child(5) {		  		background-position: center center;		  		background-size: 90%;				opacity: .5;		  	}	  	}	  }
	}}.parallax07 {		.parallaxSlick {		width: 512px;
