		background: linear-gradient($color-2nd 5%, $color-4th 50%, $color-4th 90%, $color-2nd 100%);				.banner {			position: absolute;
		    /*forces max height of background images to 1/3 = 3x retina.*/			width: 100%;			background-size: cover;
		    /*scales background images as media queries increase height of parallax-layers*/			background-repeat: no-repeat;						&:nth-child(1) {				z-index: 2;			}			&:nth-child(2) {				z-index: 3;			}			&:nth-child(3) {				z-index: 4;			}			&:nth-child(4) {				z-index: 5;			}						img {				object-fit: cover;				width: 100%;				height: inherit;				//object-position: center 100%;			}						@include media-breakpoint-up(lg) {				height: 60vh;			}						@include media-breakpoint-down(lg) {				height: 80vh;			}		}	}	}.parallax10 {	* {		color: white;	}	.skrollable {		position: fixed;				.skrollable {			position: static;						.skrollable {				position: static;			}		}	}		#progress {
