	$layer: 6; //make sure it is +1 the ammount of layer divs in the html
	$perspective: 1;	$randOffset: random(300)+px;	$distance: 3000;			/* Parallax Config */	.parallax {		height: auto;		overflow-x: hidden;		overflow-y: scroll;		perspective: #{$perspective + px};		position: relative;		background: linear-gradient(to top, #272818 50%, $color-2nd 100%); //must set bg color				.desc {			height: 100vh!important; //must set			padding-top: 100vh; //must set for banner height visible			position: relative;			background-color: transparent;			z-index: 8;			width: 100%;
			$zDepth: 2;			transform: translateZ(#{-($zDepth) + px}) scale(#{1 + (-($zDepth) * -1) / $perspective});						.mainTxt {				background: #272818;			}		}				.parallax-group {			position: relative;			height: 100%;			transform-style: preserve-3d;						.layer {				position: fixed;				margin: auto;				width: 100%;				min-height: 50vh;				height: 100vh!important; //must set				top: 0;				left: 0;				filter: drop-shadow(0 -.125rem 2rem rgba($color-2nd, .125) );								img {					width: 100%;					height: 100%;					object-fit: cover!important; //must set				}								@for $i from 1 to $layer {					&:nth-child(#{$i}) {						$zDepth: ($layer - $i)/0.5;						margin-top: #{ ($distance / $i) + px };						transform: translateZ(#{-($zDepth) + px}) scale(#{1 + (-($zDepth) * -1) / $perspective});						z-index: #{$i};					}				} //loop over				&:first-child {					margin-top: #{($distance/ $layer) + px};					filter: drop-shadow(0 0 5rem rgba(yellow, .75) );				}				&:last-child:after {					content: '';					position: absolute;					bottom: 0;					width: 100%;					height: 50%;					background: #272818;				}			}		}	}		.wrapper {				.desc {			.mainTxt {				padding: 5rem 0 0;								* {					letter-spacing: 10px;					text-transform: uppercase;					line-height: 3rem;					//font-size: 1rem;					text-align: center;					color: white;				}								p {					width: 85vw;					max-width: 960px;					margin: 0 auto;					letter-spacing: 0;					text-transform: none;				}			}		}	}}//Card CSS only.parallax18 {	.wrapper {		padding-top: 3rem;				.card {			width: 50vw;			height: 100%;			aspect-ratio: 1.5/1;			border-radius: 20px;			position: relative;			transition: 1.5s ease-in-out;			transform-style: preserve-3d;			perspective: 1;			margin: 0 auto;						&:hover {				transform: rotateY(180deg);			}						* {				font-size: 1rem;			}						.side {				position: absolute;				text-align: center;				width: 100%;				height: 100%;				padding: 3rem;				display: flex;				flex-direction: column;				justify-content: center;				align-items: center;								transform-style: preserve-3d;				backface-visibility: hidden;				border-radius: 20px;								background-size: cover;				background-position: top center;				background-repeat: no-repeat;				background-color: $color-3rd;				background-blend-mode: screen;								&.front {					z-index: 2;					background-image: url(https://userscontent2.emaze.com/images/f9538183-0ff9-478f-b964-c8ab90421e3b/3d28e192fda5c17250f96a2779c84475.jpg);				}								&.back {						transform: rotateY(180deg); //must set to fix back side position					//background-blend-mode: color-dodge;					z-index: 1;					background-image: url(https://userscontent2.emaze.com/images/f9538183-0ff9-478f-b964-c8ab90421e3b/3d28e192fda5c17250f96a2779c84475.jpg);					box-shadow: 0 0 5rem rgba($color-4th, .15);				}			}						.card-body {				transform: translatez(70px) scale(0.8);				line-height: 1.5rem;				width: 100%;				display: flex;				flex-direction: column;				justify-content: center;				align-items: center;								.card-title {					position: relative;					color: $color-main;					margin: 0;					padding-bottom: .72rem;					font-size: 1.25rem;					letter-spacing: .25rem;					text-transform: uppercase;										&:before {						content: '';						position: absolute;						bottom: 0;						height: .125rem;						background-color: $color-main;						width: calc(100% - 5rem);						left: 50%;						transform: translateX(-50%);					}				}								.card-text {					margin-top: 50px;					//line-height: 2em;					display: block;				}								form {					text-align: left;					width: 100%;					display: flex;					flex-direction: column;					justify-content: center;					gap: 0;					
					label, input {						display: block;					}										input, textarea {						background: transparent;						border: 0;						border-bottom: 2px solid $color-4th;						padding: .25rem;						width: 100%;						padding: .5rem 1rem;					}					textarea {						border: 2px solid $color-4th;						border-radius: .25rem;						min-height: 5rem;					}										label {						margin: 1rem 0 .5rem;					}										.btn {						margin-top: 2rem;					}				}			}		}	}}//Motion CSS only.parallax19 {	padding: 0!important;	$max-height: 100vh;		.heading {		z-index: 10;	}
	
	.wrapper { 
	    position:relative;
	    overflow:hidden;
	    max-height: $max-height;
	    height: 100vh;
	    width : 100%;
	
	    &:before,&:after {
	        animation-duration: 180s;
	        animation-iteration-count: infinite;  
	        
	        content:'';
	        position:absolute;
	        display:block;
	        width:100vw;
	        min-height:62.5vw;
	        top:50%;
	        left:50%; 
	
	        background-size:cover;
	        background-repeat:no-repeat;
	        background-position:80%;
	        pointer-events: none;        
	    }
	
	    // Clouds back
	    &:before {
	        background-image:url(../img/component/parallax/19/clouds4.jpg);
	        animation-name: cloud1;
	         z-index: 1;
	    }
	
	    // Clouds front
	    &:after {
	        background-image:url(../img/component/parallax/19/clouds3.png);
	        animation-name: cloud2;
	        z-index: 3;
	    }				// Blimp
		img { 
		    position:absolute;
		    right:10%; 
		    top:50%;
		    height:100vh;
		    max-height:$max-height;
		    width:auto;
		    z-index: 4;
		    animation-duration: 180s;
		    animation-iteration-count: infinite;
		    animation-name: zep;   
		}				// Birds
		[class*=bird]{
		  position: absolute;
		  // 16 x 200px
		  background: url("data:image/png;base64,") no-repeat;		  
		  width: 20px;
		  height: 16px;
		  display: block;
		  animation: bird-move 120s infinite,
		             bird-flap .8s steps(10) infinite,; 
		  z-index: 2;
		  opacity: 0.7;
		  left: -2vw;
		}
		
		.bird-1 { top:38%; animation-delay: 1s;}
		.bird-2 { top:51%; animation-delay: 2s;}
		.bird-3 { top:40%; animation-delay: 3s; transform:scale(0.8);}
		.bird-4 { top:43%; animation-delay: 4s; transform:scale(0.7);}
		.bird-5 { top:48%; animation-delay: 3s; transform:scale(0.9);}
		.bird-6 { top:50%; animation-delay: 6s;}
		
		@keyframes bird-flap { 
		    100% { background-position: 0 -200px; } 
		}
		
		@keyframes bird-move { 
		    0%   { left:-2vw; } 
		    100% { left:102vw; transform:scale(0.3);} 
		}
		
		@keyframes cloud1 {
		    0%    { transform: translate(-50%, -50%) scale(1.3) rotate(-10deg) ; }
		    20%   { transform: translate(-50%, -50%) scale(1.4) rotate(5deg);    }
		    40%   { transform: translate(-50%, -50%) scale(1);  }
		    60%   { transform: translate(-50%, -50%) scale(1.4) rotate(-5deg);   }    
		    80%   { transform: translate(-50%, -50%) scale(1.3) rotate(15deg);   }
		    100%  { transform: translate(-50%, -50%) scale(1.3) rotate(-10deg);  }    
		}
		@keyframes cloud2 {
		    0%    { transform: translate(-50%, -30%) scale(1.35) rotate(-8deg) ; }
		    20%   { transform: translate(-48%, -33%) scale(1.45) rotate(3deg);   }
		    40%   { transform: translate(-50%, -38%) scale(1.05);  }
		    60%   { transform: translate(-40%, -40%) scale(1.45) rotate(-3deg);  }    
		    80%   { transform: translate(-52%, -30%) scale(1.35) rotate(13deg) ; }
		    100%  { transform: translate(-50%, -30%) scale(1.35) rotate(-8deg) ; }
		}
		@keyframes zep {
		    0%    { transform: translate(-10%, -51%); }
		    50%   { transform: translate(50%, -75%);  }
		    75%   { transform: translate(-30%, -51%); }
		    100%  { transform: translate(-10%, -51%); }    
		}
	}}//Custom JS.parallax20 {	padding-top: 0!important;	width: 100%;	height: 100%;	overflow: hidden;		section {		padding: 0!important;	}	.wrapper {		height: 100%;		overflow-x: hidden;		overflow-y: auto;		clip-path: border-box;		transform: translateZ(0);	}		._block {		position: relative;		height: 100vh;	}		[class*='item-parallax'] {		position: absolute;		width: 100%;		height: 100%;		transition: transform 0.3s ease-out;		will-change: transform; // prevents paint flashing	}		.item-parallax-media {		z-index: 1;
		  
		  img {			will-change: transform; // prevents paint flashing		}	}		.item-parallax-content {		z-index: 2;	}
	
	
	$boxes: 5;
	@for $i from 1 through $boxes {		.block:nth-child( #{$i} ) .item-parallax-content {			transform: translateY( calc( var(--yBlock-#{$i}) * 0.5px ) );		}				.block:nth-child( #{$i} ) .item-parallax-media img {			transform: translateY( calc( var(--yBlock-#{$i}) * 0.1px ) );		}	}
	
	
	// --------------- layout
	
	$dark: #00171f;
	$light: white;
	
	@mixin font($type:regular) {		@if $type == head {			font-family: 'Inknut Antiqua', serif;		}				@if $type == regular {			font-family: 'Roboto', sans-serif;		}				@if $type == condensed {			font-family: 'Roboto Condensed', sans-serif;		}	}		@mixin font-smoothing($value: on) {		@if $value == on {			-webkit-font-smoothing: antialiased;			-moz-osx-font-smoothing: grayscale;		}				@else {			-webkit-font-smoothing: subpixel-antialiased;			-moz-osx-font-smoothing: auto;		}	}		html {		background-color: $dark;	}		.flex-container {		display: flex;	}		.centered-content {		margin: auto;	}		.head-large {		margin-bottom: 5vw;		font-size: 10vw;		line-height: 1.2em;
		  @include font(head);		color: $light;	}		.head-small {		margin-bottom: 1.5rem;		font-size: 1.2rem;		line-height: 1.2em;
		  @include font(condensed);		color: $light;		text-transform: uppercase;		letter-spacing: 0.5em;
		  @include font-smoothing(on);	}		.head-centered {		text-align: center;	}		.copy {		display: block;		max-width: 500px;		margin-right: auto;		margin-bottom: 1.5rem;		margin-left: auto;		padding-right: 1.5rem;		padding-left: 1.5rem;
		  @include font(regular);		font-size: 1rem;		line-height: 1.8em;		text-align: center;	}		.copy-white {		color: $light;
		  @include font-smoothing(on);	}		.item-parallax-media {		overflow: hidden;
		  
		  img {			position: absolute;			top: 0%;			left: 0%;			width: 100%;			height: 100%;			object-fit: cover;		}	}
	
	// ------------ landing
	.section-landing:before {		content: '';		position: absolute;		top: 50%;		left: 24%;		width: 10px;		height: 80%;		background-color: white;		z-index: 3;		transform: translateY(-13vw);	}		.section-landing .item-parallax-media {		opacity: calc( 1 - (var(--y) * 0.0009) );	}		.landing-content {		padding-left: 30%;	}
	
	// ---------------- intro
	.section-intro:before {		content: '';		position: absolute;		top: 50%;		left: 50%;		width: 10px;		height: 80%;		background-color: mix($light, $dark, 5%);		transform: translateX(-50%) 
	    translateY(-50%) 
	    rotate( calc( (var(--y) * 0.1deg) ) );		will-change: transform;		backface-visibility: visible;	}
	
	
	// ------------------ image grid
	.img-grid {		flex-direction: column;		justify-content: space-around;	}		.img-gridItem {		position: relative;		width: 100%;		max-width: 800px;		margin-right: auto;		margin-left: auto;
		  
		  img {			display: block;			width: 50%;			height: 40vh;			object-fit: cover;			will-change: transform;			backface-visibility: visible;		}				&.type-left img {			margin-right: auto;		}				&.type-right img {			margin-left: auto;		}	}		.img-gridItem {		&:nth-child(1) img {			transform: translateX( calc( var(--yBlock-4) * -0.03% ) );		}				&:nth-child(2) img {			transform: translateX( calc( var(--yBlock-4) * 0.03% ) );		}	}		.img-caption {		position: absolute;		top: 50%;		width: 50%;		transform: translateY(-50%);
		  
		  * {			text-align: left;			padding-right: 1.5rem;			padding-left: 1.5rem;		}				.type-left & {			right: 0;		}				.type-right & {			left: 0;		}	}
	
	
	// ---------------- end
	.section-end:before {		content: '';		position: absolute;		bottom: 50%;		left: 24%;		width: 10px;		height: 80%;		background-color: white;		z-index: 3;		transform: translateY(8vw);	}}