/* All Section Config */main, article {	//background-color: $bg-default;		section {		background-position: top center;		background-repeat: no-repeat;		background-size: cover;		padding: 3.75rem 0;		overflow: hidden;				* {			transition: ease-out .2s;		}				@include media-breakpoint-down(xl) {			padding: 2.5rem 0;		}	}		> section:first-child:not( .hero-banner ) {		@include media-breakpoint-up(lg) {			padding-top: 8rem;		}		@include media-breakpoint-down(lg) {			padding-top: 5rem;		}	}		.mainContent, .content, .tool-editor {		> *:not( iframe ) {	  		/* Reset all to defaul */	  		all: revert;			}		img {			max-width: 100%;		}		a:hover {			color: $color-main;		}		p {			margin-top: .875rem;			margin-bottom: .875rem;		}		iframe {			width: 100%;			min-height: 350px;						&.video {				width: 100%;				aspect-ratio: 16 / 9;			}		}	}}/* Title Config */.heading {	display: flex;	flex-direction: column;	align-items: center;	text-align: center;	color: $color-main;		@include media-breakpoint-down(xl) {		gap: .25rem;	}		h2.underline {		position: relative;		padding-bottom: 1rem;				&::before {			content: '';			position: absolute;			bottom: 0;			left: 50%;			transform: translateX(-50%);			width: 5rem;			height: .125rem;			background: $color-2nd;			border-radius: 1rem;		}	}		&.left {		h2 {			text-align: left;						&.underline {				&::before {					left: 0;					transform: translateX(0);				}			}		}	}	.title, .subtitle {		width: 100%;		font-weight: 800;		color: inherit;		text-align: inherit;		text-transform: uppercase;		line-height: 1.5;				@include media-breakpoint-up(xl) {			max-width: 880px;		}		@include media-breakpoint-only(lg) {			max-width: 880px;
		}		@include media-breakpoint-down(lg) {			max-width: 90vw;			line-height: 1.4;		}	}		.title {		@include media-breakpoint-up(xl) {			font-size: 2rem;		}		@include media-breakpoint-only(lg) {			font-size: 1.75rem;
		}		@include media-breakpoint-down(lg) {			font-size: 1.5rem;		}	}		.subtitle {		color: $color-main;				@include media-breakpoint-up(xl) {			font-size: 1.25rem;		}		@include media-breakpoint-only(lg) {			font-size: 1.125rem;
		}		@include media-breakpoint-down(lg) {			font-size: 1.25rem;		}	}		.text, .tool-editor {		margin-top: 2rem;		//margin-bottom: 1rem;		color: black;				@include media-breakpoint-down(lg) {			text-align: justify;
		}				& > * {			color: inherit!important;			text-align: inherit!important;		}	}		.btn {		margin-top: 2rem;	}		.all-revert {		*:not( iframe ) {	  		/* Reset all to defaul */	  		all: revert;			}	}		.text-truncate {		&:not( :has(*) ) {			max-width: 30ch; //ch mean charater			display: inline-block;		}				* {			display: inline-block;			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;			max-width: 30ch;		}	}		.nav-tabs {		margin-bottom: 2rem;	}}/* Navtab has ScrollSpy Config */.block:has( .ScrollSpy) {	overflow: visible;}.nav-tabs {	border: 0;	gap: 1rem;		@include media-breakpoint-only(lg) {		width: 50%;		display: flex;		flex-direction: row;		gap: 1rem;		justify-content: center;		align-items: center;
	}		&.sticky {		position: -webkit-sticky;
		position: sticky;		z-index: 99;				@include media-breakpoint-up(xl) {			top: 3.3125rem;			transform: scale(.75);		}		@include media-breakpoint-down(xl) {			top: 3.75rem;		}	}		.nav-item {		.nav-link {			border-radius: 0;			font-weight: bold;			text-transform: uppercase;			color: $color-main;			border: 2px solid $color-main;			background: none;						@include media-breakpoint-up(lg) {				padding: 0.625rem 2rem;				font-size: .875rem;			}			@include media-breakpoint-down(lg) {				padding: .5rem 1.25rem;				font-size: .75rem;			}						&.active {				background: $color-main;				color: white;				border-color: transparent;			}						&:hover {				background: $color-main;				color: white;			}		}	}}/* Block List Config */.list-block, .block-list {	margin-bottom: 1.5rem;	display: flex;	flex-direction: row;	align-items: flex-start;	justify-content: flex-start;	flex-wrap: wrap;	gap: 1.5rem;		@include media-breakpoint-only(lg) {		gap: 1.125rem;	}	@include media-breakpoint-only(md) {		gap: 1.5rem;
	}	@include media-breakpoint-down(md) {		gap: 1rem;	}	@include media-breakpoint-down(lg) {		[class*='card'], > * {			flex-basis: 47.5%;		}	}	@include media-breakpoint-up(lg) {		&.list-noWarp {			flex-wrap: nowrap;		}		&.list-2 {			& > * {				flex-basis: 48%;			}			}		&.list-3 {			& > * {				flex-basis: 31.95%;			}		}		&.list-4 {			& > * {				flex-basis: 24%;			}		}		&.list-5 {			& > * {				flex-basis: 18%;			}		}		&.list-6 {			& > * {				flex-basis: 14.5%;			}		}	}}/* Breadcrumb Config */.breadcrumb {
  margin: 0 0 1rem;    * {  	color: white;  }

  &-item {  	//text-transform: uppercase;
    font-size: .875rem;
    padding-right: 1.5rem !important;	padding-left: 0 !important;
    position: relative;		display: flex;	flex-direction: row;	gap: 0;	align-items: center;

    /*&:first-child {
      padding-left: 0 !important;
    }*/

    .breadcrumb-link {
      //color: $color-main;	  	  &:hover {	  	color: $color-2nd;	  }
    }		&::before {		content: ''!important;  	}  	&::after {
	    content: "";
	    position: absolute;
	    width: 0;
	    //top: 50%;
	    right: 0;
	    height: 0;
	    transform: translateX(-150%);
	    border-top: 3px solid transparent;
	    border-left: 5px solid white;
	    border-bottom: 3px solid transparent;
	  }  }
}
/* Pagination Config */
.pagination {
  column-gap: .625rem;

  .icon {
    color: $color-main;
    font-size: 2.5em;
    height: 25px;
    line-height: .8;
  }

  .page-item.disabled {
    .page-link {
      border: 1px solid $gray;

      .icon {
        color: white;
      }
    }
  }

  .page-item.active {
    .page-link {
      background: $color-main;
      color: white;	  border-color: $color-main;	  opacity: 1;

      .icon {
        color: white;
      }
    }
  }

  .page-link {
    background-color: transparent;
    border: 1px solid;	//border-color: $color-2nd;
    text-transform: uppercase;
    color: inherit;
    font-weight: bold;
    font-size: .875em;
    line-height: inherit;
    min-height: 40px;
    min-width: 40px;
    border-radius: .25rem!important;
    text-align: center;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;	//opacity: .75;

    @include media-breakpoint-down(sm) {
      border-radius: 5px !important;
    }

    &:hover {
      background: $color-2nd;	  border-color: $color-2nd;
      color: white;

      .icon {
        color: white;
      }
    }
  }
}.powerby {	display: none;}/* iFrame Config */iframe.video, iframe[src*="youtube.com/"], iframe[src*="vimeo.com/"] {	width: 100%;	aspect-ratio: 16 / 9;}/* Overlay Config */.blur {	section {		filter: blur(.75rem);	}}.overlay {	position: fixed;	top: 0;	left: 0;	width: 100vw;	height: 100vh;	background-color: rgba( $color-2nd , .75);	z-index: 999;	display: flex;	flex-direction: column;	justify-content: center;	align-items: center;	gap: 2rem;		&.hide {		display: none!important;	}		&.intro {		background-color: rgba( $color-2nd , .95);	}		.logo {		height: 3rem;		backface-visibility: visible !important;
		animation: bounce-in 2s ease infinite;	}		.spinner-border {		color: $color-main!important;	}}/* Button Config */.btn, .btn:visited {
	display: inline-block;
	font-family: $font;
	font-weight: 800;
	font-size: .875rem;
	text-transform: uppercase;
	background: $color-main;	color: white;
	padding: .625rem 2rem;
	border-radius: 0;	border: 2px solid transparent;
	@include transition(all, .25s);
  
	@include media-breakpoint-up(lg) {		font-size: .875rem;		padding: 10px 2rem;	}	@include media-breakpoint-down(lg) {		//font-size: .75rem;		font-size: .875rem;		padding: .65rem 1.25rem;	}

	&:hover, &:active {
		//background: white;
		//color: $color-main;
		box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .1);
	}	&.more {		background: $color-2nd;		color: white;				&:hover, &:active, &:visited {			background: white;
			color: $color-main;		}	}	&.reset, &.reset:visited {		background: $gray;		color: $gray-light;		opacity: .5;				&:hover, &:active {			opacity: 1;		}	}	&.btn-primary {		background: transparent;
		color: $color-main;		border-color: $color-main;				&:hover, &:active {			background: $color-main;
			color: white;
			box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .1);		}	}	&.no-outline {		border: 0!important;		background: transparent;	}		i {		margin: 0 .25rem;	}
}/* Slick Config */.slick-loading .slick-list {
    background: #fff url('../img/ajax-loader.gif') center center no-repeat;
}.slider {	/*&.infinitySmoothSlide {		.slick-track {			//remove all trasition effect to make infinity smooth slide			transition: none;		}	}*/	.slick-track {		//remove all trasition effect to make infinity smooth slide		transition: none;	}	.slick-slide {		.img-fluid {			width: 100%;		}	}	@include media-breakpoint-up(sm) {		&.slick-slider .slick-list {		}	}		&.slick-dotted.slick-slider {		margin-bottom: 0!important;	}		.slick-arrow {		position: absolute;		color: $color-main;		z-index: 2;		top: 50%;		transform: translateY(-50%);				i {			font-size: 3.135rem;			color: inherit;		}				&:hover {			i {				color: $color-2nd;				//filter: drop-shadow(0px 0px 5px rgba( $color-main, 1) );			}		}				&.btn-prev {			left: 2rem;		}		&.btn-next {			right: 2rem;		}	}		.slick-dots {		margin: .75rem 0 0 0;		position: static;				li {			width: 1rem;			height: 5px;			margin: 0 8px;						button {				height: inherit;				width: inherit;				//background-color: rgba( $color-main, .75);				background-color: $color-main;				padding: 0;				border-radius: 5px;								&:before {					content: '';				}			}						&.slick-active button, &:hover button {				background-color: $color-2nd;			}		}	}		&.no-dots {		.slick-dots {			display: none!important;		}	}	&.no-arrows {		.slick-arrow {			display: none!important;		}	}}/* Title Page Config */
.title-page {	visibility: hidden;	position: absolute;	z-index: -1;}
/* Card Config */
.card {
	margin: 0;
	border: 0;
	background: transparent;

    .card-img-top {
      object-fit: cover;	  aspect-ratio: 16 / 11;
	  border-radius: 0;
    }

    .card-body {		display: flex;		flex-direction: column;		gap: 0;		line-height: 1.5;		//padding-bottom: 0;		//padding: 1rem 0;
		
		.card-title {
	        padding: 0;
	        font-size: 1rem;
	        font-family: $font;
	        font-weight: bold;
	        overflow: hidden;
	        display: -webkit-box;
	        -webkit-line-clamp: 2;
	        -webkit-box-orient: vertical;
			color: inherit;
			line-height: inherit;
		}

		.card-text {
			font-size: 1rem;
			padding: 0;
			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			line-height: inherit;
			color: inherit;
		}				.stretched-link {			opacity: 0;			height: 0!important;			padding: 0;			margin: 0;		}
	}		&:hover {		.card-body {			.card-title {				color: $color-2nd;			}		}	}
}/* Collapse Config */.collapse-block {	.heading {		width: 100%;		align-items: flex-start;		order: 0;				* {			text-align: left;		}				.nav-tabs {			align-items: center;			justify-content: flex-start;			margin: 2rem 0 1rem;						@include media-breakpoint-only(lg) {
			}			@include media-breakpoint-down(lg) {				justify-content: space-around;			}						.nav-item {				@include media-breakpoint-down(lg) {					flex: 1 0 47%;										.nav-link {						text-align: center;					}				}								.nav-link {					display: flex;					flex-direction: row;					gap: .5rem;					align-items: center;					justify-content: center;					position: relative;										@include media-breakpoint-down(lg) {						@media (max-width: 375px) {							font-size: .65rem;						}					}										i {						display: none;						position: absolute;						left: 1rem;						font-size: 1rem;												@include media-breakpoint-down(lg) {							left: .5rem;						}					}										&.active {						i {							display: block;						}					}				}			}		}	}		.collapse-wrapper {		@include media-breakpoint-only(md) {			padding: 2rem!important;
		}				.item {			display: flex;			flex-direction: column;			flex-wrap: wrap;			padding: 1rem 0;			border-bottom: 1px solid rgba( #999999, .15);						&-link {				display: flex;				flex-direction: row;				align-items: center;				justify-content: space-between;				gap: 1rem;								.text {					color: inherit;					font-size: 1rem;					font-weight: bold;					padding: .5rem 0;					text-align: left;										> * {				  		/* Reset all to defaul */				  		color: inherit;				  		all: revert;					  		margin: 0;				  		padding: 0;					}				}				i {					color: inherit;					font-size: 1.5rem;				}			}						&-link:has( + .collapse.show) {				* {					color: $color-main;				}				i {					transform: rotate(180deg);				}			}						&:last-child {				border-bottom: 0;			}			.collapsing {				 * {					opacity: 0;				}			}			.collapse {				.text {					display: inline-block;										> *:not( iframe ) {				  		/* Reset all to defaul */				  		all: revert;						}				}			}		}	}}