.floating-menu {	position: fixed;	z-index: 100;	display: flex;	align-items: center;		@include media-breakpoint-up(lg) {		bottom: 1.5rem;		right: 1.5rem;		flex-direction: column;		justify-content: flex-end;		gap: 1rem;	}	@include media-breakpoint-only(md) {		bottom: .75rem;		right: .75rem;
	}	@include media-breakpoint-down(lg) {		bottom: 1vh;		right: 1vw;		justify-content: center;		align-items: center;		z-index: 9;	}		* {		transition: ease-out .2s;	}		img {		aspect-ratio: 1 / 1;		object-fit: cover;				@include media-breakpoint-up(lg) {			//min-width: 3.75rem;			width: 5.25vh;		}				@include media-breakpoint-only(md) {			width: 3.75vh;
		}	}		a {		border-radius: 10rem;		aspect-ratio: 1 / 1;		display: flex;		align-items: center;		justify-content: center;		background: $color-main;		min-width: 2rem;				@include media-breakpoint-down(lg) {			height: 100%;		}				img {			width: 3.75rem;						@include media-breakpoint-down(lg) {				width: 2.75rem;			}		}		&:hover {			background: $color-2nd;			//filter: drop-shadow(0px 0px 10px rgba( $color-main , 1) );						img {				border-radius: 10rem;				animation: bubble-color-2nd 4s 0.1s infinite alternate;				//filter: invert(0.65) sepia(1) saturate(10) hue-rotate(-30deg);			}		}		&.back2top {			display: none;			background: $color-2nd;						@include media-breakpoint-down(lg) {				position: absolute;				bottom: 3.5rem;				right: .25rem;				width: 2.125rem;				height: 2.125rem;			}			i {				font-size: 1.25rem;				color: white;			}			img {				@include media-breakpoint-up(lg) {					width: 2rem;					//min-width: 2rem;				}								@include media-breakpoint-down(lg) {					width: auto;				}			}			&.show {
				display: flex;								&:hover {					background: $color-2nd;				}
			}		}	}}footer {	position: relative;	background-color: $color-main;	color: white;	z-index: 1;	display: flex;	flex-direction: row;	justify-content: flex-end;	align-items: center;	flex-wrap: nowrap;	gap: 1rem;	padding-top: 3.75rem;		@include media-breakpoint-up(xl) {
	}
	@include media-breakpoint-down(xl) {		padding: 2rem 0 1rem;				.container {			//--bs-gutter-x: 0;			padding: 0;		}
	}	/*@include media-breakpoint-down(md) {		padding-bottom: 50px;	}*/		* {		color: inherit;		transition: ease-out .2s;				i {			font-size: 1rem;		}	}	a {		color: white;		font-size: .875rem;				&:hover {			color: $color-2nd;			transition: ease-out .2s;		}	}		.logo-brand {		.logo-image {			@include media-breakpoint-up(lg) {				width: 250px;			}			@include media-breakpoint-down(lg) {				width: 12rem;			}		}	}		.list-footer {		position: relative;				@include media-breakpoint-down(xl) {			padding: 0;
		}	}		.sub-menu {		display: flex;		justify-content: space-between;		width: 100%;		padding-bottom: 3.75rem;				@include media-breakpoint-up(lg) {			flex-direction: row;		}		@include media-breakpoint-only(md) {			flex-direction: row;			flex-wrap: wrap;			gap: 2rem;
		}		@include media-breakpoint-down(md) {			flex-direction: column;			//padding-left: .5rem;			//padding-right: .5rem;			gap: 1rem;			padding-bottom: 1rem;		}				a {			font-weight: bold;		}				.col-item {			display: flex;			flex-direction: column;			gap: .5rem;						@include media-breakpoint-up(lg) {				width: 19%;								&:nth-child(2) {					width: 30%;				}			}			@include media-breakpoint-only(md) {				flex-basis: 100%;
			}			@include media-breakpoint-down(lg) {				padding: 1rem;								&:first-child {					.text {						text-align: justify;					}				}			}						.info {				display: flex;				flex-direction: row;				gap: .5rem;				justify-content: flex-start;				align-items: center;				font-size: .875rem;								&.addr {					align-items: baseline;				}								a {					font-size: .875rem;					text-transform: none;					font-weight: normal;				}			}						&:nth-child(2), &:nth-child(3) {				@include media-breakpoint-only(md) {					flex-basis: 47.5%;
				}			}						&:last-child {				@include media-breakpoint-down(lg) {					flex-direction: row;					flex-wrap: wrap;					justify-content: space-between;										h6 {						flex-basis: 100%;					}										.content, .social {						flex-basis: 47%;					}				}			}						.col-item-wrapper {				display: flex;				flex-direction: column;				gap: 1rem;			}						h6 {				font-weight: bold;								@include media-breakpoint-down(lg) {					padding-bottom: .5rem;					//border-bottom: 1px solid rgba( $color-main , .5);					border-bottom: 1px solid $color-main;				}			}						.bct img {				width: 6.875rem;			}		}	}		.social {		display: flex;		flex-direction: row;		gap: 1rem;		justify-content: flex-start;		align-items: center;		flex-wrap: wrap;				@include media-breakpoint-down(lg) {			//flex-basis: 45%;		}				.icon {			font-size: 2rem;			color: $color-2nd;						&:hover {				color: white;			}		}				.bct {			flex-basis: 100%;		}			}		.sub-footer {		display: flex;		justify-content: space-between;		align-items: center;		padding: .75rem 0;		border-top: 1px solid $color-2nd;				@include media-breakpoint-up(lg) {			flex-direction: row;		}		@include media-breakpoint-down(lg) {			flex-direction: column;			gap: .5rem;			align-items: flex-start;			padding-left: 1rem;			//margin-right: .5rem;		}				* {			font-size: .75rem;		}				.privacy {			i {				margin-right: .25rem;			}		}	}}.toast {	position: fixed;	bottom: 40px;	right: 6rem;	background-color: $red;	z-index: +1;		width: -webkit-fit-content;	width: fit-content;		* {		color: white;	}	.btn-close, .btn-close:hover {		background: none;		border: 0;		opacity: 1;				&:after {			content: '';		}	}}