/* Pages in health */.prune-nutrition {	@include media-breakpoint-down(lg) {		padding: 0;
	}				.container {		display: flex;		flex-direction: row;		gap: 1rem;		align-items: center;				@include media-breakpoint-down(lg) {			flex-direction: column;						& > * {				width: 100%;								img {					margin: 0 auto;				}			}		}				.info {			img {				@include media-breakpoint-up(lx) {					max-width: 31.25rem;
				}				@include media-breakpoint-only(lg) {					max-width: 35vw;
				}				@include media-breakpoint-down(lg) {					width: 100%;
				}			}		}		.prune {			img {				object-fit: cover;								@include media-breakpoint-up(lg) {					//max-width: 250px;					max-width: 15vw;				}			}		}		.provide {			@include media-breakpoint-up(lg) {				width: 25rem;			}		}	}}.prune-kid {	.item {		border: 1px solid $color-main;		border-radius: .25rem;		padding: 1rem 2rem;		margin-bottom: 1rem;				@include media-breakpoint-down(lg) {			border-radius: .625rem;
		}				.tool-editor {			color: $text-color;						& > * {				color: inherit!important;			}		}	}}