article[article-detail] {		.prune-titlepage {		@include media-breakpoint-up(xl) {			min-height: 500px;
		}
		@include media-breakpoint-down(xl) {			min-height: 300px;
		}				&:before {			background: rgba( $color-main, 1);		}		.title {			text-align: left;			line-height: 1.5;		}		.date {			margin-top: 1rem;			color: white;			font-style: italic;			font-size: .875rem;		}	}}.article.articleDetails {	padding-top: 0;	margin-top: -2rem;	position: relative;	z-index: 3;	overflow: unset!important;		.container {		@include media-breakpoint-up(lg) {			display: flex;			flex-direction: row;			gap: 1rem;			align-items: flex-start;		}		@include media-breakpoint-only(md) {			//padding: 0;
		}	}	.action {		display: inline-flex;		flex-direction: column;		gap: .5rem;		align-items: center;				@include media-breakpoint-up(lg) {			position: sticky;			top: 6rem;			//right: -4rem;			margin-top: 3.5rem;
		}
		@include media-breakpoint-down(lg) {
		}				a {			padding: .25rem;			width: 40px;			height: 40px;			font-size: 1rem;			border-radius: 2rem;			cursor: pointer;						border: 1px solid $color-main;			background-color: white;			color: white;			display: flex;			flex-direction: column;			gap: 0;			justify-content: center;			align-items: center;						&:hover {				color: white;				background-color: $color-main;								i {					color: white;				}			}						i {				color: $color-main;			}		}	}}.article .articleMain {	background-color: white;	border-radius: 1rem;	padding: 0;	position: relative;		/*@include media-breakpoint-down(lg) {		padding: 1rem;	}*/		.article-thumbnail {		width: 100%;		object-fit: cover;				@include media-breakpoint-up(lg) {			min-height: 200px;		}	}		.tool-editor {		img {			margin: 2rem 0 1rem;		}	}		.title {		color: $color-main;		text-align: left;		font-weight: bold;				@include media-breakpoint-up(lg) {			font-size: 1.5rem;		}	}	.action {		margin-top: 1rem;		flex-direction: row;	}}