/* All Modal Config */.modal {	[class*='close'] {		position: absolute;
		top: -.5rem;
		right: -.5rem;		padding: .01rem .15rem;		background-color: white;		display: inline-block;		border-radius: 10rem;		box-shadow: 0px 3px 10px rgba(black , .15);				@include media-breakpoint-down(lg) {			right: 0;			top: -1.5rem;		}				i {			font-size: 2.5rem;			color: $color-main;		}	}		.modal-content {		overflow: visible;		padding: 1.5rem;		border-radius: 0;		
		@include media-breakpoint-down(xl) {			padding: .25rem;
		}	}	.btn, .btn:visited {		font-weight: bold;				&:hover, &:active {			background-color: white;			color: $color-main;		}	}	.modal-body {		padding: 2rem 2rem 1rem;				@include media-breakpoint-down(sm) {			padding: 1.5rem;		}	}	.modal-footer {		justify-content: center;		border: 0;	}}/* Success Modal Config */.modal.success {	.modal-content {		border: 0;		border-radius: 0;		background: rgba($color-2nd, .75);		backdrop-filter: blur(.25rem);
		-webkit-backdrop-filter: blur(.25rem);		color: white;				&:before {			content: '';			position: absolute;			border: 1px solid $color-main;			border-radius: .75rem;			@include calc('width', '100% - 2rem');			@include calc('height', '100% - 2rem');			z-index: -1;			top: 1rem;			left: 1rem;		}		.modal-body {			* {				color: inherit;			}		}	}}/* Error Modal Config */.modal.error {	.modal-content {		.modal-body {			> * {				color: red;			}		}	}}/* Comment Modal Config */.modal.comment {	.modal-content {		background-color: $color-3rd;		border-radius: 1rem;				/*@include media-breakpoint-up(lg) {			padding: 1.5rem;		}*/				.modal-body {			@include media-breakpoint-down(lg) {				padding: 0;			}		}				h2 {			color: white;			font-size: 1rem;			font-weight: bold;			text-transform: uppercase;			margin-bottom: 1rem;		}				form {			display: grid;			grid-template-columns: repeat(2, 1fr);			gap: 1.25rem;						.m-form {				justify-self: stretch;				grid-column: 1 / 3;								&:nth-child(1) {					grid-column: 1 / 2;				}				&:nth-child(2) {					grid-column: 2 / 3;				}				&:nth-child(3) {					grid-column: 1 / 3;										textarea {						width: 100%;												&.parsley-error {							border: 1px solid red;						}					}				}								input, textarea {					border-radius: .75rem;					background: $color-3rd;					color: white!important;					box-shadow: none;					border: 1px solid white;					padding: .5rem 1rem;				}								input::placeholder {					color: white!important;					opacity: .75;				}			}			p.notice {				grid-column: 1 / 3;				color: white;				font-size: .75rem;			}			.btn {				background: $color-3rd;				color: white!important;				border: 1px solid white;								&:hover {					background: white;					border: 2px solid white;					color: $color-3rd!important;				}			}						[class*='parsley-errors-'] {				color: $color-main;			}		}	}}/* Search */.modal.search {	/*.modal-dialog-centered {		@include media-breakpoint-down(lg) {			align-items: flex-start;			padding-top: 7rem;		}	}*/	.modal-content {		background: transparent;		margin: 0;		padding: 0;		border: 0;				[class*=close] {			z-index: 2;		}				.modal-body {			margin: 0;			padding: 0;			padding-top: 5rem;						@include media-breakpoint-down(lg) {				padding-top: 1.25rem;
			}						form.searchForm {				width: 100%;				border: 2px solid rgba(255, 255, 255, 1);			}		}	}}.modal-backdrop {	--bs-backdrop-opacity: 0.85;}