/* Pages in recipe *//*article > section.prune-titlepage-recipe {	padding-top: 4rem!important;}*/.prune-titlepage-recipe {		position: relative;	background-position: center;	overflow: visible;			display: flex;	flex-direction: column;	justify-content: flex-end;		@include media-breakpoint-up(xl) {		height: 520px;		margin-bottom: 20rem;	}	@include media-breakpoint-only(lg) {		margin-bottom: 10rem;	}	@include media-breakpoint-down(lg) {		min-height: 300px;	}		&:before {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;		background: rgba( $color-main, .8);		z-index: 1;	}	&:after {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;
		background: linear-gradient(0deg,rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.2) 100%);		z-index: 1;	}		.container {		position: relative;		z-index: 2;		display: flex;		flex-direction: row;		justify-content: flex-start;		align-items: flex-end;		gap: 0;				@include media-breakpoint-up(xl) {			bottom: -20rem;		}		@include media-breakpoint-only(lg) {			bottom: -10rem;		}	}		.left {		display: flex;		flex-direction: column;		gap: 5rem;		justify-content: flex-start;		width: 60%;		margin-bottom: 10rem;		padding: 0 4rem;				@include media-breakpoint-only(lg) {			width: 70%;			margin-bottom: 5rem;			gap: 2rem;		}		@include media-breakpoint-down(lg) {			width: 100%;			padding: 0;			margin-bottom: 0;						.heading .text.tool-editor {				display: none;			}
		}	}		.recipe-search {		display: flex;		flex-direction: row;		justify-content: flex-start;		align-items: center;		gap: 0;				//border: 1px solid $color-main;		//border-right: none;		//border-radius: .25rem;				.form-control, .form-select {			border: 1px solid $color-main;			border-radius: 0;			box-shadow: none;			min-height: 50px;		}		.form-select {			width: 10rem;			border-right: none;		}		.form-control {			border-left: none;			border-right: none;		}		.btn, .btn:visited {			min-height: 50px;						@include media-breakpoint-up(lg) {				max-height: 50px;
			}			@include media-breakpoint-only(lg) {				padding: 5px 1rem;				font-size: .75rem;			}						&:hover, &:active {				box-shadow: none;				background: $color-2nd;				color: $color-main;			}		}		.m-form:has(.form-control) {			//width: 54.95%;			width: 54%;						@include media-breakpoint-only(lg) {				width: 52%;			}		}	}		.heading {		display: flex;		flex-direction: column;		gap: 1rem;		justify-content: flex-start;				@include media-breakpoint-down(lg) {			justify-content: center;		}				h1.title {			color: white;			text-transform: uppercase;			font-weight: 800;			font-size: 2rem;			width: 100%;			text-align: left;						@include media-breakpoint-down(lg) {				text-align: center;				font-size: 1.25rem;			}		}		.tool-editor {			margin-top: 0;			color: white;						& > * {				color: inherit!important;			}		}	}		.featured {		position: relative;		width: 28vw;		position: absolute;		bottom: 0;		right: 0;				.title {			position: absolute;			top: 3rem;			left: -2rem;			z-index: 3;			color: white;			text-transform: uppercase;			font-weight: bold;			font-size: 1.125rem;			background: $color-2nd;			padding: .5rem 1rem;		}				.slider {			.slick-arrow {				display: none!important;			}		}				.recipe-card.feature {			max-height: 600px;		}	}		@include media-breakpoint-down(lg) {		.featured, .recipe-search {			display: none;		}
	}}.recipe-list {	padding-top: 0;		.block-recipe-mobile {		@include media-breakpoint-up(lg) {			display: none	;
		}		@include media-breakpoint-down(lg) {			.heading .tool-editor {				text-align: justify;								& > * {					text-align: inherit!important;				}			}			form.recipe-search {				margin-bottom: 3rem;								.m-form {					margin-bottom: .5rem;				}				.btn {					width: 100%;					border-radius: .25rem;				}			}			.featured {				display: flex;				flex-direction: column;				justify-content: center;				gap: 1rem;				margin-bottom: 3rem;								.title {					margin: 0 auto;					max-width: 60vw;					background: $color-2nd;					padding: .75rem;					text-align: center;					font-size: 1.125rem;					color: white;					font-weight: bold;				}								.slider {					.slick-arrow {						display: none!important;					}				}			}		}	}		.topic {		@include media-breakpoint-down(lg) {			justify-content: center;						.nav-item {				flex-basis: 45.5%;				width: 100%;			}
		}				.nav-link {			background: white;			color: $color-main;			font-size: .75rem;			font-weight: 800;			text-transform: uppercase;			padding: .5rem 1rem;			border: 2px solid $color-main;			border-radius: .25rem;						@include media-breakpoint-down(lg) {				text-align: center;			}		}	}		.list-block {		margin-top: 2rem;	}}