/* Pages in contact */.prune-contact {		.container {		display: flex;		flex-direction: row;		justify-content: space-between;		gap: 1rem;		align-items: flex-start;		
		@include media-breakpoint-down(lg) {			flex-direction: column;			gap: 3rem;
		}	}		.contact-info {		@include media-breakpoint-up(lg) {			width: 40%;			text-align: left;		}				.heading {			margin-bottom: 2rem;						.title {				text-align: left;				line-height: 1;								&.underline::before {					left: 0;					transform: translate(0);				}			}		}				.info-list {			display: flex;			flex-direction: column;			gap: .5rem;			justify-content: flex-start;						h6 {				text-align: left;				font-weight: bold;				font-size: 1rem;				color: $color-main;			}						.info {				display: flex;				flex-direction: row;				gap: .5rem;				justify-content: flex-start;				align-items: center;								&.addr {					align-items: baseline;				}								a {					font-size: 1rem;					text-transform: none;					font-weight: normal;										&:hover {						color: $color-2nd;					}				}								i {					font-size: 1rem;					color: $color-2nd;				}			}		}				.more-list {					}	}		.contact-form {		@include media-breakpoint-up(lg) {			min-width: 500px;			width: 50%;		}				.heading {			margin-bottom: 1rem;			justify-content: flex-start;			align-items: flex-start;						border-bottom: 1px solid $color-main;						.subtitle {				text-align: left;				line-height: 1;								&.underline::before {					left: 0;					transform: translate(0);				}			}						.tool-editor {				text-align: left;				margin-top: 0;								& > * {					text-align: inherit!important;				}			}		}				form {			display: flex;			flex-direction: column;			gap: 1rem;			align-items: flex-start;			justify-content: flex-start;						*:not(.btn) {				width: 100%;			}			.m-form:has(.btn) {				text-align: left!important;			}			.notice {				font-size: .5625rem;				font-style: italic;			}			.btn, .btn:visited {				&:hover, &:active {					background: $color-2nd;					color: white;				}			}		}	}}