.m-form, .form-check {	label {		margin-bottom: .25rem;	}}.m-form {	input[type=file] {		//border: 0;		color: transparent!important;		padding: .25rem;				&::-webkit-file-upload-button {
			visibility: hidden;
		}		&::before {
			content: 'Chọn ảnh';			color: #333;
			display: inline-block;
			background: linear-gradient(to bottom, #f9f9f9, #e3e3e3);
			border: 1px solid #999;
			border-radius: 1rem;
			padding: .25rem 1.125rem;
			outline: none;
			white-space: nowrap;
			-webkit-user-select: none;
			cursor: pointer;
			
			font-weight: bold;
		}		&:hover::before {
			border-color: black;
		}		&:active::before {
			background: linear-gradient(to bottom, #e3e3e3, #f9f9f9);
		}	}}.form-control, .form-select, textarea, [class*=parsley-success] {
	font-size: 100%;
	//box-shadow: 0px .15rem .25rem rgba(0,0,0, .1);
	border: 1px solid;	border-color: rgba($gray, .25);
	padding: .45rem 1.25rem;
	border-radius: .25rem;	min-height: 50px;	background-color: var(--bs-body-bg)!important;		@include media-breakpoint-down(xl) {		font-size: .875rem;	}
	
	&.parsley-error, &.parsley-required {
		border-color: #EF4923 !important;		//color: #EF4923 !important;		background-color: rgba(255, 0, 0, .75);
	}
}.form-control, .form-select, textarea {	&::placeholder {		color: $gray;		font-weight: 300;		//font-style: italic;	}}textarea.form-control {
    min-height: calc(10em + 0.75rem + calc(var(--bs-border-width) * 2))
}.form-check {	.form-check-input {}	.form-check-label {}}.range, input[type=range] {	-webkit-appearance: none;
	appearance: none; 
	width: 100%;
	cursor: pointer;
	outline: none;
	border-radius: 15px;
	height: 6px;
	background: rgba( $color-main , .25);	//filter: invert(1);		&:before, &:after {		color: white;	}		&::-webkit-slider-thumb {
		-webkit-appearance: none;
		appearance: none; 
		height: 15px;
		width: 15px;
		background-color: $color-main;		//filter: invert(1);
		border-radius: 50%;
		border: none;
		transition: .2s ease-in-out;
	}		&::-moz-range-thumb {
		height: 15px;
		width: 15px;
		background-color: $color-main;		//filter: invert(1);
		border-radius: 50%;
		border: none;
		transition: .2s ease-in-out;
	}
		
	&::-webkit-slider-thumb:hover {
		box-shadow: 0 0 0 10px rgba(255,85,0, .1)
	}
	&:active::-webkit-slider-thumb {
		box-shadow: 0 0 0 13px rgba(255,85,0, .2)
	}
	&:focus::-webkit-slider-thumb {
		box-shadow: 0 0 0 13px rgba(255,85,0, .2)
	}
	
	&::-moz-range-thumb:hover {
		box-shadow: 0 0 0 10px rgba(255,85,0, .1)
	}
	&:active::-moz-range-thumb {
		box-shadow: 0 0 0 13px rgba(255,85,0, .2)
	}
	&:focus::-moz-range-thumb {
		box-shadow: 0 0 0 13px rgba(255,85,0, .2)    
	}}[class*=parsley-errors-] {
  color: #EF4923;
  font-size: .75rem;
  font-style: italic;
  margin-top: 3px;
  position: relative;  flex-basis: 100%;    li {  	@include media-breakpoint-down(xl) {  		margin-top: .5rem;	  	line-height: 1.35;	  	font-size: .65rem;  	}  }
}