.navbar {	position: fixed;	z-index: 98;	top: 0;	display: flex;	gap: 0;	padding: 0;	width: 100%;	transition: ease-out .2s;		@include media-breakpoint-up(lg) {		&:hover {			z-index: 1000;		}	}	@include media-breakpoint-up(xl) {		//height: 106px;		//padding-top: 1.125rem;				> [class*='container'] {			position: relative;		}	}	@include media-breakpoint-down(xl) {		background: rgba( $color-main, 1);		height: 60px;					&:has( .navbar-collapse.show ) {			z-index: 1000;		}	}		&.transparent-menu {		background: transparent!important;	}		&.onscroll {		background: rgba( $color-main, .95);		backdrop-filter: blur(.25rem);
		-webkit-backdrop-filter: blur(.25rem);
		//background: linear-gradient(0deg, rgba( $color-main ,0) 0%, rgba( $color-main ,1) 75%);		transition: ease-out .2s;				@include media-breakpoint-up(xl) {			height: 80px;		}				.logo-brand .logo-image {			@include media-breakpoint-up(xl) {				width: 8rem;			}			@include media-breakpoint-down(xl) {				width: 6.75rem;			}		}	}		.top-menu {		position: absolute;		right: 1rem;		top: .75rem;		text-align: right;		gap: 1.35rem;				@include media-breakpoint-only(lg) {			right: 4rem;		}		@include media-breakpoint-down(xl) {			display: none;		}				.nav-link {			i {				font-size: 1.125rem;			}						&:hover {				i {					color: white;				}			}		}	}		* {		transition: ease-out .2s;	}	@include media-breakpoint-down(xl) {		[class*='container'] {			padding: 0;		}	}		.logo-brand {		position: absolute;		top: 0;		background: $color-main;		padding: 1.5rem .92rem;		border-radius: 0 0 1rem 1rem;		box-shadow: 0px 10px 30px rgba(0, 0, 0, .25);				@include media-breakpoint-down(xl) {			left: 1rem;			z-index: 999;			box-shadow: none;			padding: .35rem 0 0;			background: transparent;		}				.logo-image {			@include media-breakpoint-up(xl) {				width: 10rem;			}			@include media-breakpoint-down(lg) {				width: 7.125rem;			}			@include media-breakpoint-only(lg) {				width: 7rem;			}		}	}		/* on mobile */	.navbar-toggler-holder {		display: flex;		align-items: center;		gap: 1rem;		position: absolute;		right: 0;		top: 0;		z-index: 998;		padding-top: .75rem;		padding-right: .75rem;				@include media-breakpoint-down(lg) {			justify-content: flex-end;			flex-direction: row;						.top-menu {				position: relative;				display: flex;				flex-direction: row;				gap: 1rem;				top: 0;				right: 0;								.nav-link i {					font-size: 1.5rem;				}			}		}				* {			color: white;		}				.lang-switch {			padding-top: .25rem;						img {				width: 1.5rem;			}		}				.navbar-toggler {			border: 0;			padding: 0;			width: 2rem;			height: 2rem;			display: flex;			justify-content: center;			align-items: center;						i {				font-size: 1.75rem;			}						.btn-close {				filter: none;				display: block;				width: 1.5em;
			    height: 1.5em;
			    vertical-align: middle;				opacity: 1;			}			.navbar-toggler-icon {
	          display: none;
	        }						&.collapsed {
		        .btn-close {
		          display: none;
		        }
		
		        .navbar-toggler-icon {
		          display: block;
		        }			}		}	}		.navbar-collapse {		padding-top: 2.5rem;		align-items: center;				@include media-breakpoint-up(xl) {			display: flex;			flex-direction: row;			justify-content: flex-end;				}		@include media-breakpoint-only(lg) {			flex-basis: 40%;			width: 40%;			padding-top: 4.5rem;			&.show {				//width: 80vw;			}
		}		@include media-breakpoint-down(md) {			&.show {				width: 80vw;							.navbar-nav {					width: 95vw;					max-height: 85vh;					overflow-y: scroll;					padding-top: 1rem;					padding-bottom: 1rem;					justify-content: flex-start;					gap: .35rem;				}			}		}		@include media-breakpoint-down(xl) {						&:after {				 content: '';				 position: absolute;				 top: 0;				 right: -100vw;				 width: 100vw;				 height: 100vh;				 background: rgba($color-main, 1);				 z-index: -1;				 transition: ease-out .5s;				 				 @include media-breakpoint-only(lg) {				 	//width: 0;				 	background: rgba(black, .75);				 	height: 91.9vh;				 	top: unset;				 	bottom: 0;				 }			}						&.show {				display: flex;				//width: 80vw;				position: relative;				height: 100vh;				transition: ease-out .2s;								@include media-breakpoint-only(lg) {					justify-content: flex-end;				}							&:after {					 right: 0;					 transition: ease-out .2s;				}								.navbar-nav {					min-height: 75vh;					//width: 85vw;					left: 0;					transition: ease-out .5s;										@include media-breakpoint-only(lg) {						height: 93.7vh;						min-width: 35vw;						overflow: scroll;						left: unset;						right: 0;
					}				}			}						.navbar-nav {				position: absolute;				left: -100%;								padding: 1rem;				border-radius: 0 1rem 1rem 0;				background-color: $color-main;				position: relative;								display: flex;				flex-direction: column;				justify-content: center;				gap: 1rem;				transition: ease-out .2s;								@include media-breakpoint-only(lg) {					border-radius: 0;					left: unset;					right: -100%;				}				@include media-breakpoint-between(md, lg) {					padding: 1rem 3rem;					gap: 2rem;
				}								&:before {					content: '';					position: absolute;					top: 0;					left: -1rem;					width: 50%;					height: 100%;					background-color: $color-main;				}			}		}	}		ul {		display: flex;		justify-content: flex-end;		gap: 1.25rem;		color: inherit;						li.nav-item {			display: inline-flex;			color: inherit;						@include media-breakpoint-up(xl) {				&.dropdown:hover {					> .dropdown-menu {						display: block;					}				}			}						@include media-breakpoint-down(xl) {				flex-direction: column;			}						&.lang-switch .nav-link {				&::before {					width: 0;					height: 0;				}				&:hover {					filter: drop-shadow(0px 0px 5px rgba(255, 255, 255, .75) );				}			}					.nav-link {				color: $color-2nd;				//font-family: $font;				font-size: .9rem;				font-weight: bold;				//letter-spacing: 1.05px;				position: relative;				overflow: hidden;				transition: ease-out .2s;								> * {					color: inherit;					font-weight: inherit;				}								@include media-breakpoint-up(xl) {					/*padding-top: 0;					padding-bottom: 0;*/					min-height: 1.875rem;					display: flex;
					align-items: center;
					justify-content: center;										/*&::before {						content: '';						position: absolute;						bottom: .25rem;						left: var(--bs-navbar-nav-link-padding-x);						background-color: $color-2nd;						border-radius: 1rem;						width: 3rem;						height: .25rem;						transform: translateX(-3.5rem);						opacity: 0;						transition: ease-out .2s;					}					&:hover::before {						transform: translateX(0);						opacity: 1;						transition: ease-out .2s;					}*/				}				@include media-breakpoint-down(xl) {					padding-left: 1rem;					padding-right: 1rem;					color: white;				}								&.active {					color: white;										@include media-breakpoint-up(xl) {						&::before {							transform: translateX(0);							opacity: 1;						}					}					@include media-breakpoint-down(xl) {						background-color: rgba( $color-2nd , .1);						//color: white;						color: $color-2nd;					}				}								&.dropdown-toggle {					&:after {						margin-left: .5rem;												@include media-breakpoint-down(lg) {							cursor: pointer;							position: absolute;							top: 50%;							right: 1rem;							transform: translateY(-50%);						}					}					&:hover + .dropdown-menu {						display: block;					}				}								img {					width: 100%;					max-width: 1.5rem;				}			}			.dropdown-menu {				@include media-breakpoint-up(xl) {					display: none;				}				@include media-breakpoint-down(xl) {					display: flex!important;					flex-direction: column;					gap: 1rem;					background: none;					border-radius: 0;										.dropdown-item {						padding: .65rem 1rem!important;						color: white!important;												&.active {							color: $color-2nd!important;						}					}				}			}						.colorMode {				& + .dropdown-menu .dropdown-item {					display: flex;					flex-direction: row;					gap: .5rem;					align-items: center;					justify-content: flex-start;										i {						font-size: 1.25rem!important;						font-weight: normal;					}				}			}		}				//hover effect only navbar-nav		&.navbar-nav {			li.nav-item {				.nav-link {					@include media-breakpoint-up(xl) {						&::before {							content: '';							position: absolute;							bottom: .25rem;							left: var(--bs-navbar-nav-link-padding-x);							background-color: $color-2nd;							border-radius: 1rem;							width: 3rem;							height: .25rem;							transform: translateX(-3.5rem);							opacity: 0;							transition: ease-out .2s;						}						&:hover::before {							transform: translateX(0);							opacity: 1;							transition: ease-out .2s;						}					}				}			}		}	}}.nav-link:has(+ .dropdown-menu:hover) {	@include media-breakpoint-up(xl) {		&::before {			transform: translateX(0);			opacity: 1;		}	}}.dropdown {	height: auto!important;	border-bottom: 0;		> .dropdown-menu {		display: block;		text-align: center;		border: 0;		border-radius: 0;		background: white;				padding: 0;		display: flex;
		flex-direction: row;		justify-content: flex-start;		gap: 0;		box-shadow: 0px .25rem 1rem rgba(black, .15);				@include media-breakpoint-up(xl) {			margin-top: 2.25rem;			//top: 0;		}		@include media-breakpoint-down(xl) {			margin-top: .5rem;			box-shadow: none;
		}					> li {			width: 100%;			border-radius: 0;						@include media-breakpoint-up(xl) {				&:first-child > .dropdown-item {					//border-radius: 1rem 1rem 0 0;					//overflow: hidden;				}				&:last-child > .dropdown-item {					//border-radius: 0 0 1rem 1rem;					border-radius: 0;					overflow: hidden;				}				&:only-child {					//border-radius: 1rem;					//overflow: hidden;				}			}						.dropdown-item {				padding: 0;				background: transparent;				text-align: left;				padding: .875rem 1.5rem;				color: $color-main;				font-size: .9rem;				font-weight: bold;								> * {					color: inherit;					font-weight: inherit;				}								@include media-breakpoint-up(xl) {					&:hover {						background-color: $color-main!important;						color: $color-2nd;					}				}				@include media-breakpoint-down(xl) {					margin-left: 1rem;					//margin-right: 1rem;					width: calc(100% - 1rem);										& + .dropdown-menu .dropdown-item {						margin-left: 2rem;						width: calc(100% - 2rem);					}				}				&.active {					background-color: rgba( $color-main , .75);					color: white;				}			}						&.sub {				@include media-breakpoint-up(xl) {					position: relative;										> .dropdown-menu {						margin-top: 0;						//transform: translate(-.5rem, -3.125rem);						top: 0;						left: 100%;						box-shadow: 0px 10px 30px rgba(0, 0, 0, .25);					}										&:hover {						> .dropdown-item {							background: $color-main;							color: white;						}						> .dropdown-menu {							display: block;						}					}				}								.dropdown-item.dropdown-toggle {					position: relative;										@include media-breakpoint-up(xl) {						padding-right: 2.5rem;					}										&:after {						transform: rotate(-90deg) translateX(-100%);						position: absolute;						right: 1rem;												@include media-breakpoint-down(xl) {							top: 50%;							transform: rotate(0deg) translateY(-100%);						}					}				}			}		}	}}.popover {	z-index: 99;	border-radius: 0;	border: 1px solid $color-main;	background-color: $color-main;	box-shadow: 0px 10px 30px rgba(0, 0, 0, .25);		color: white;		.popover-arrow {		--bs-popover-arrow-border: #D71921;		--bs-popover-bg: #D71921;		/*&:before {			border-left-color: $color-main!important;		}		&:after {			border-left-color: black!important;		}*/	}	.popover-header {		background-color: $color-main;		color: inherit;		font-weight: bold;				&::before {			width: 0!important;		}				h3 {			font-weight: inherit;		}		i {			font-size: 2rem;		}	}	.popover-body {		background-color: black;		color: inherit;				 * {		 	color: inherit;		 	line-height: 1.5;		 }		 		 .building {		 	margin-bottom: .5;		 	text-transform: uppercase;		 }	}}.tooltip {	background-color: black;	border-radius: 0;	* {		text-align: left;		line-height: 1.3;	}}