/* Animation *///use animation-direction: alternate to Play the animation forward first, then backward.hiThere {	animation: hiThere 4s 0.1s infinite alternate;}@include setKeyFrame('hiThere',	(		30%: (			transform: scale(1.2)		),		40%: (			transform: rotate(-20deg) scale(1.2)		),		50%: (			transform: rotate(20deg) scale(1.2)		),		60%: (			transform: rotate(-20deg) scale(1.2)		),		70%: (			transform: rotate(0deg) scale(1.2)		),		100%: (			transform: scale(1)		),	));.rollIn {	animation: rollIn 4s 0.1s infinite alternate;}@include setKeyFrame('rollIn',	(		0%: (			opacity: 0,
    		transform: translateX(-100%) rotate(-120deg)		),				100%: (			opacity: 1,
    		transform: translateX(0px) rotate(0deg)		),	));.rollOut {	animation: rollOut 4s 0.1s infinite alternate;}@include setKeyFrame('rollOut',	(		0%: (			opacity: 1,
    	transform: translateX(0px) rotate(0deg)		),				100%: (			opacity: 0,
    		transform: translateX(100%) rotate(120deg)		),	));.wobble {	animation: wobble 4s 0.1s infinite alternate;}@include setKeyFrame('wobble',	(		0%: (			transform: translateX(0%)		),		15%: (			transform: translateX(-25%) rotate(-5deg)		),		30%: (			transform: translateX(20%) rotate(3deg)		),		45%: (			transform: translateX(-15%) rotate(-3deg)		),		60%: (			transform: translateX(10%) rotate(2deg)		),		75%: (			transform: translateX(-5%) rotate(-1deg)		),		100%: (			transform: translateX(0%)		),	));.grow {	animation: grow 4s 0.1s infinite alternate;}@include setKeyFrame('grow',	(		0%: (			transform: scale(0)		),				100%: (			transform: scale(1)		),	));.gelatine {	animation: gelatine 4s 0.1s infinite alternate;}@include setKeyFrame('gelatine',	(		0%: (			transform: scale(1, 1)		),		25%: (			transform: scale(0.9, 1.1)		),		50%: (			transform: scale(1.1, 0.9)		),		75%: (			transform: scale(0.95, 1.05)		),		100%: (			transform: scale(1, 1)		),	));.bounceIn {	animation: bounceIn 4s 0.1s infinite alternate;}@include setKeyFrame('bounceIn',	(		0%: (			opacity: 0,			transform: translateY(0)		),		40%: (			opacity: 1,			transform: translateY(-30px)		),		50%: (			transform: translateY(0)		),		60%: (			transform: translateY(-15px)		),		80%: (			transform: translateY(0)		),	));.float {	animation: float 4s 0.1s infinite alternate;}@include setKeyFrame('float',	(		0%: (			transform: translatey(0)		),		50%: (			transform: translatey(-35%)		),	));.flipMaxx {	animation: flipMaxx 4s 0.1s infinite alternate;}@include setKeyFrame('flipMaxx',	(		0%: (			transform: perspective(400px) rotateY(0) scale(1)		),		25%: (			transform: perspective(400px) rotateY(180deg) scale(1.5)		),		50%: (			transform: perspective(400px) rotateY(360deg)  scale(1.5)		),		75%: (			transform: perspective(400px) rotateY(360deg) scale(.5)		),		100%: (			transform: perspective(400px) rotateY(0) scale(1)		),	));.FadeUp {	animation: FadeUp 4s 0.1s infinite alternate;}@include setKeyFrame('FadeUp',	(		0%: (			opacity: 0,			transform: translateY(100%),		),		50%: (			opacity: 0.5,			transform: translateY(-5%),		),		60%: (			opacity: 1,			transform: translateY(0%),		),	));.FadeDown {	animation: FadeDown 4s 0.1s infinite alternate;}@include setKeyFrame('FadeDown',	(		0%: (			opacity: 0,			transform: translateY(-100%),		),		50%: (			opacity: 0.5,			transform: translateY(5%),		),		60%: (			opacity: 1,			transform: translateY(0%),		),	));.FadeLeft {	animation: FadeLeft 4s 0.1s infinite alternate;}@include setKeyFrame('FadeLeft',	(		0%: (			opacity: 0,			transform: translateX(100%),		),		50%: (			opacity: 0.5,			transform: translateX(-2%),		),		60%: (			opacity: 1,			transform: translateX(0%),		),	));.FadeRight {	animation: FadeRight 4s 0.1s infinite alternate;}@include setKeyFrame('FadeRight',	(		0%: (			opacity: 0,			transform: translateX(-100%),		),		50%: (			opacity: 0.5,			transform: translateX(2%),		),		60%: (			opacity: 1,			transform: translateX(0%),		),	));.shadowFade {	animation: shadowFade 4s 0.1s infinite alternate;}@include setKeyFrame('shadowFade',	(		from: (			filter: drop-shadow(0px 0px 0px rgba( $color-main, 0) )		),		to: (			filter: drop-shadow(0px 0px 30px rgba( $color-main, 1) )		),	));.borderFade {	border: 3px solid white;	border-radius: 50%;	animation: borderFade 4s 0.1s infinite alternate;}@include setKeyFrame('borderFade',	(		from: (			border-color: transparent		),		to: (			border-color: $color-main		),	));.spỉn3D {	animation: spỉn3D 4s 0.1s infinite alternate;}@include setKeyFrame('spỉn3D',	(		0%: (			transform: rotateX(0deg) translate(0) rotateZ(0deg)		),		50%: (			transform: rotateX(0deg) translate(0) rotateZ(0deg)		),		100%: (			transform: rotateX(360deg) translate(0) rotateZ(-360deg)		),	));.changeModeBg {	animation: changeModeBg 4s 0.1s infinite alternate;}@include setKeyFrame('changeModeBg',	(		0%: (			background-blend-mode: $blend-mode,			background: $color-2nd,		),		25%: (			background-blend-mode: $blend-mode,			background: $color-3rd,		),		50%: (			background-blend-mode: $blend-mode,			background: $color-2nd,		),		75%: (			background-blend-mode: $blend-mode,			background: $color-3rd,		),		100%: (			background-blend-mode: $blend-mode,			background: $color-2nd,		),	));.fadeIn {	animation: fadeIn 4s 0.1s infinite alternate;}@include setKeyFrame('fadeIn',	(		0%: (			opacity: 0		),		100%: (			opacity: 1		)	));.spin {	animation: spin 4s 0.1s infinite alternate;}@include setKeyFrame('spin',	(		from: (			transform:rotate(0deg)		),		to: (			transform:rotate(360deg)		)	)
);.flip {	animation: flip 4s 0.1s infinite alternate;}@include setKeyFrame('flip',	(		0%: (			transform: rotateY(0deg)		),		50%: (			transform: rotateY(180deg)		),		100%: (			transform: rotateY(360deg)		)	)
);.shake {	animation: shake 4s 0.1s infinite alternate;}@include setKeyFrame('shake',	(		0%: (			transform: translateX(0%) rotate(0deg)		),		3%: (			transform: translateX(-30px) rotate(-6deg)		),		6%: (			transform: translateX(15px) rotate(6deg)		),		9%: (			transform: translateX(-15px) rotate(-3.6deg)		),		12%: (			transform: translateX(9px) rotate(2.4deg)		),		15%: (			transform: translateX(-6px) rotate(-1.2deg)		),		18%: (			transform: translateX(0) rotate(0)		),		100%: (			transform: translateX(0%) rotate(0deg)		)	));.push {	animation: push 4s 0.1s infinite alternate;}@include setKeyFrame('push',	(		50%: (			transform: scale(0.8)		)	));.pop {	animation: pop 4s 0.1s infinite alternate;}@include setKeyFrame('pop',	(		50%: (			transform: scale(1.2)		)	));.lightEffect {	animation: lightEffect 4s 0.1s infinite alternate;}@include setKeyFrame('lightEffect',	(		0%: (			opacity: 1,			filter: drop-shadow(0px 0px 10px rgba( $color-main, .75) ) drop-shadow(0px 0px 10px rgba( $color-main, .55)) drop-shadow(0px 0px 10px rgba( $color-main, .35) )		),		25%: (			opacity: 0.4,			filter: drop-shadow(0px 0px 10px rgba( $color-main, .55) ) drop-shadow(0px 0px 10px rgba( $color-main, .35) ) drop-shadow(0px 0px 10px rgba( $color-main, .15) )		),		50%: (			opacity: 1,			filter: drop-shadow(0px 0px 10px rgba( $color-main, .75) ) drop-shadow(0px 0px 10px rgba( $color-main, .55)) drop-shadow(0px 0px 10px rgba( $color-main, .35) )		),		75%: (			opacity: 0.4,			filter: drop-shadow(0px 0px 10px rgba( $color-main, .55) ) drop-shadow(0px 0px 10px rgba( $color-main, .35) ) drop-shadow(0px 0px 10px rgba( $color-main, .15) )		),		100%: (			opacity: 1,			filter: drop-shadow(0px 0px 10px rgba( $color-main, .75) ) drop-shadow(0px 0px 10px rgba( $color-main, .55)) drop-shadow(0px 0px 10px rgba( $color-main, .35) )		)	));.tilt {	animation: tilt 4s 0.1s infinite alternate;}@include setKeyFrame('tilt',	(		0%: (			transform: rotateX(-30deg)		),		25%: (			transform: rotateX( 30deg)		),		50%: (			transform: rotateY(-30deg)		),		75%: (			transform: rotateY( 30deg)		),		100%: (			transform: rotateZ( 20deg)		)	));.swingSide {	animation: swingSide 4s 0.1s infinite alternate;}@include setKeyFrame('swingSide',	(		0%: (			transform: translate3d(15%,0,0) rotateZ(10deg)		),		100%: (			transform: translate3d(-15%,0,0) rotateZ(-10deg)		)	));.wave {	animation: wave 4s 0.1s infinite alternate;}@include setKeyFrame('wave',	(		0%: (			transform: rotateZ(0deg) translate3d(0,10%,0) rotateZ(0deg)		),		100%: (			transform: rotateZ(360deg) translate3d(0,10%,0) rotateZ(-360deg)		)	));.swingCenter {	animation: swingCenter 4s 0.1s infinite alternate;}@include setKeyFrame('swingCenter',	(		0%: (			transform: rotateZ(10deg)		),		100%: (			transform: rotateZ(-10deg)		)	));.bubble {	border-radius: 50%;	animation: bubble 4s 0.1s infinite alternate;}@keyframes bubble {
    0% {
        box-shadow:0 0 8px 6px rgba($color-main, 0), 0 0 0 0 rgba($color-main, 0), 0 0 0 0 rgba($color-main, 0)
    }
    10% {
        box-shadow:0 0 8px 6px rgba($color-main, .5), 0 0 12px 10px rgba($color-main, 0), 0 0 12px 14px rgba($color-main, .5)
    }
    100% {
        box-shadow:0 0 8px 6px rgba($color-main, 0), 0 0 0 40px rgba($color-main, 0), 0 0 0 40px rgba($color-main, 0)
    }
}@include animation-mixin(bubble-color-2nd, $color-2nd);@include animation-mixin(bubble-color-white, #ffffff	);