/* Pages in about */.prune-fact {	min-height: 600px;	position: relative;	background-position: center;		@include media-breakpoint-only(lg) {		min-height: 500px;	}	@include media-breakpoint-down(lg) {		padding: 3.75rem;	}		&:before {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;		background: rgba( $color-main, .75);		z-index: 1;	}		.container {		position: relative;		z-index: 2;		color: white;		display: flex;		flex-direction: column;		justify-content: center;		align-items: center;		gap: 2rem;				& > * {			color: inherit;		}				.heading {			color: white;						& > * {				color: white;			}			.underline::before {				background: white;			}		}				.fact-list {			display: flex;			align-content: center;						@include media-breakpoint-up(lg) {				flex-direction: row;				justify-content: space-around;				gap: 3.75rem;
			}
			@include media-breakpoint-down(lg) {				flex-direction: column;				justify-content: center;				gap: 2.5rem;
			}						.item {				display: flex;				flex-direction: column;				justify-content: center;				align-items: center;				gap: 0;				//padding: 0 2rem;								@include media-breakpoint-down(lg) {					&:nth-child(1) {						order: 1;					}					&:nth-child(2) {						order: 0;					}					&:nth-child(3) {						order: 2;					}				}								.title {					color: $color-2nd;					font-size: 5rem;					font-weight: 900;					text-align: center;										@include media-breakpoint-down(lg) {						line-height: 1.2;					}										sub, sup {						font-weight: inherit;						font-size: 3rem;					}				}				.tool-editor {					color: white;					font-size: 1.25rem;					//font-weight: bold;					//text-align: center;					p {						margin: 0;					}				}				.logo {					width: 20rem;										@include media-breakpoint-only(lg) {						width: 15rem;					}					@include media-breakpoint-down(lg) {						width: 10rem;					}				}			}		}	}}.prune-practice {	background: $color-main;	color: white;		@include media-breakpoint-down(lg) {		padding: 4rem 0;	}		.heading {		color: white;		& > * {			color: inherit;		}		.underline:before {			background: white;		}		.tool-editor {			color: white;						& > * {				color: inherit!important;			}		}	}		.practice-list {		margin-top: 3.5rem;				.item {			padding: 0 1.5rem;			display: flex;			flex-direction: column;			align-items: center;			gap: 1rem;			color: white;			margin-bottom: 2rem;						@include media-breakpoint-down(lg) {				margin-bottom: 0;			}						&-img {				max-width: 6rem;				aspect-ratio: 1/1;				}						.title {				text-align: center;				color: inherit;				font-size: 1.25rem;				font-weight: 700;			}			.tool-editor {				color: white;								@include media-breakpoint-down(lg) {					text-align: justify;					margin-top: 0;				}								& > * {					color: inherit!important;					text-align: inherit!important;				}			}		}				&.slider {			.slick-arrow {				color: $color-3rd;								&.btn-prev {					left: -3rem;				}				&.btn-next {					right: -3rem;				}			}			.slick-dots {				li {					button {						background-color: rgba(255, 255, 255, .3);					}					&.slick-active button, &:hover button {						background-color: $color-2nd;					}				}			}		}	}}.prune-timeline {	.timeline-list {		display: flex;		flex-direction: column;		gap: .25rem;		margin-top: 2rem;				@include media-breakpoint-up(lg) {			align-items: center;
		}
		@include media-breakpoint-down(lg) {			align-items: flex-start;
		}				.start-point img {			width: 3rem;						@include media-breakpoint-down(lg) {				width: 2.5rem;				margin-left: .25rem;			}						}		.end-point img {			width: 5rem;						@include media-breakpoint-down(lg) {				width: 3rem;			}		}		.main-point {			display: flex;			flex-direction: column;			gap: 1rem;			width: 100%;			position: relative;			padding: 1rem 0;						&:before {				content: '';				position: absolute;				top: 0;				width: .125rem;				height: 100%;				background: $color-2nd;								@include media-breakpoint-up(lg) {					left: 50%;					transform: translateX(-50%);
				}
				@include media-breakpoint-down(lg) {					left: 6.5%;					transform: translateX(-50%);
				}			}		}				.item {			min-height: 200px;						.milestone {				display: inline-block;				background: $color-2nd;				font-size: 100%;				font-weight: 800;				color: white;				padding: .25rem .5rem;				text-transform: uppercase;			}			.tool-editor {				margin-top: .5rem;								& > * {					text-align: inherit!important;				}			}						@include media-breakpoint-up(lg) {				&:nth-child(odd) {					padding-right: calc(50% + 3rem);					text-align: right;										.tool-editor {						 text-align: right;					}				}				&:nth-child(even) {					padding-left: calc(50% + 3rem);					text-align: left;										.tool-editor {						 text-align: left;					}				}				&:nth-child(n+2) {					margin-top: -3rem;				}			}			@include media-breakpoint-down(lg) {				text-align: left;				padding-left: 3.5rem;			}						&.present {				width: 100%;				padding: 0;								@include media-breakpoint-up(lg) {					max-width: 40rem;					margin-top: 2rem;					text-align: center;
				}
				@include media-breakpoint-down(lg) {					margin-top: 1rem;					text-align: left;
				}								.tool-editor {					 text-align: center;					 					 @include media-breakpoint-down(lg) {					 	text-align: left;					 }					 					 & > * {					 	text-align: inherit!important;					 }				}			}		}	}}.prune-mission {	margin: 1.5rem auto 0;	display: flex;	max-width: 1080px;		@include media-breakpoint-up(lg) {		flex-flow: row wrap;		row-gap: 3.5rem;		align-items: stretch;		justify-content: space-between;
	}
	@include media-breakpoint-down(lg) {		flex-direction: column;		gap: 3rem;
	}		.item {		background: $color-main;		color: white;		padding: 0 2rem 1rem;		border-radius: .25rem;		position: relative;				display: flex;		flex-direction: column;		justify-content: center;				@include media-breakpoint-up(lg) {			width: 48.5%;		}		@include media-breakpoint-down(lg) {			width: 100%;		}				&:last-child {			width: 100%;		}				.title {			color: $color-main;			font-size: 1.25rem;			font-weight: 700;			text-transform: uppercase;			background: $color-2nd;			padding: .5rem 1rem;			border-radius: .25rem;			display: inline-block;									@include media-breakpoint-up(lg) {				position: absolute;				top: -1.5rem;			}			@include media-breakpoint-down(lg) {				margin-top: -1.5rem;				text-align: center;			}		}				.tool-editor {			margin-top: 2.5rem;			color: inherit;						@include media-breakpoint-down(lg) {				margin-top: 1rem;				text-align: justify;			}						& > * {				color: inherit!important;				text-align: inherit!important;			}		}	}}.prune-faq {	padding-top: 0!important;	position: relative;	background-position: center;	background-size: cover;		display: flex;	flex-direction: column;	justify-content: flex-end;		&:before {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;		background: rgba( $color-main, .9);		z-index: 1;	}	&:after {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;
		background: linear-gradient(0deg,rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.2) 100%);		z-index: 1;	}		.container {		position: relative;		z-index: 2;		display: flex;		flex-direction: column;		justify-content: flex-end;				&:first-child {			min-height: 400px;			padding-bottom: 3.75rem;						@include media-breakpoint-only(lg) {				min-height: 300px;			}		}				&.maxW-960 {			@include media-breakpoint-only(lg) {				max-width: 744px;
			}		}	}		h1.title {		color: white;		text-transform: uppercase;		font-weight: 800;		font-size: 2rem;		width: 100%;		text-align: center;	}		.faq-list {		display: flex;		flex-direction: column;		gap: 1.5rem;				.item {			//normal			.item-link {				color: $color-2nd;				font-weight: bold;				font-size: 1rem;				border: 1px solid $color-2nd;				border-radius: .25rem;				padding: .5rem 1rem;				background: transparent;								display: flex;				flex-direction: row;				justify-content: space-between;				align-items: center;				gap: 1rem;								i {					font-size: 2rem;				}								.tool-editor {					color: inherit;					font-weight: inherit;					font-size: inherit;										& > * {						color: inherit!important;						font-weight: inherit!important;						font-size: inherit!important;					}					@include media-breakpoint-down(xl) {						p {							margin: .35rem 0;						}
					}				}			}						&:hover {				.item-link {					background: white;					border-color: white;					color: $color-main;				}			}						//shown | active			.item-link:has( + .collapse.show) {				background: white;				border-color: white;				color: $color-main;				border-radius: .25rem .25rem 0 0;								i {					transform: rotate(180deg);				}			}			.collapse.show {				border: 1px solid white;				color: white;				border-radius: 0 0 .25rem .25rem;				padding: .5rem 1rem;								.tool-editor {					color: inherit;										& > * {						color: inherit!important;					}				}			}		}	}}