/* Form Config */[data-bs-theme=dark] {}[data-bs-theme=light] {}[data-bs-theme] {	--bs-body-error-color: yellow;}.container-xxl, .container-xl, .container-lg, .container-md, .container-sm, .container {	&.maxW-600 {		@include media-breakpoint-up(xl) {			max-width: 600px;		}	}	&.maxW-800 {		@include media-breakpoint-up(xl) {			max-width: 800px;		}		@include media-breakpoint-only(lg) {			max-width: 600px;		}	}	&.maxW-960 {		@include media-breakpoint-up(xl) {			max-width: 960px;		}	}	&.maxW-1080 {		@include media-breakpoint-up(xl) {			max-width: 1080px;		}	}	&.maxW-1180 {		@include media-breakpoint-up(xl) {			max-width: 1180px;
		}	}
}.card-large {	display: flex;	align-items: center;	margin: 0 auto;		@include media-breakpoint-up(lg) {		flex-direction: row;		gap: 3.75rem;		max-width: 1080px;	}	@include media-breakpoint-down(lg) {		flex-direction: column;		gap: 1.25rem;	}		.card-img-top {		order: 0;		object-fit: cover;		min-height: 400px;				@include media-breakpoint-up(lg) {			flex-basis: 45%;			width: 45%;			max-width: 510px;			height: 100%;		}		@include media-breakpoint-down(lg) {			width: 100%;			//aspect-ratio: 1/1!important;			height: 400px!important;		}				&.yt {			position: relative;						&:before {				content: '';				position: absolute;				top: 0;				left: 0;				width: 100%;				height: 100%;				background: rgba( $color-main, .3);				z-index: 1;			}					img {				width: 100%;				height: 100%;				object-fit: cover;			}			i {				position: absolute;				top: 50%;				left: 50%;				transform: translate(-50%, -50%);				color: red;				font-size: 5rem;				z-index: 2;			}						&:hover {				i {					color: $color-2nd;				}			}		}	}		.card-body {		order: 1;				@include media-breakpoint-up(lg) {			width: 50%;			flex-basis: 50%;		}		@include media-breakpoint-down(lg) {			text-align: center;		}				.card-title {			font-size: 1.25rem;			color: $color-main;			font-weight: bold;			padding-bottom: 1rem;			margin-bottom: 1.25rem;			position: relative;						@include media-breakpoint-down(lg) {				text-align: center;			}						&::before {				content: '';				position: absolute;				bottom: 0;				width: 5rem;				height: .125rem;				background: $color-2nd;				border-radius: 1rem;								@include media-breakpoint-down(lg) {					left: 50%;					transform: translate(-50%);				}			}		}		.tool-editor {			@include media-breakpoint-down(lgs) {				& > * {					text-align: justify!important;				}			}		}		.btn, .btn:visited {			margin-top: 1.25rem;			border-color: $color-main;			background: transparent;			color: $color-main;						&:hover, &:active{				background: $color-main;				color: white;			}		}	}		&.right {		.card-img-top {			@include media-breakpoint-up(lg) {				order: 1;			}			@include media-breakpoint-down(lg) {				order: 0;			}		}		.card-body {			@include media-breakpoint-up(lg) {				order: 0;			}			@include media-breakpoint-down(lg) {				order: 1;			}					}	}		&.full {		.card-img-top {			height: 100%!important;		}	}	&.vertical {		flex-direction: column;				.card-img-top {			order: 1;			width: 100%;			max-width: 100%;			max-height: 300px;		}		.card-body {			order: 0;			width: 100%;		}	}}.recipe-card {	background: transparent;	border-radius: 0;		.card-img-top {		aspect-ratio: 380/420;				/*@include media-breakpoint-up(lg) {			aspect-ratio: 380/420;			//min-height: 420px;
		}
		@include media-breakpoint-down(lg) {			//aspect-ratio: 1/1.2;			aspect-ratio: 380/420;			min-height: 190px;
		}*/	}	.card-body {		margin-top: -2rem;				@include media-breakpoint-down(lg) {			padding: .5rem;			margin-top: -1.5rem;		}				.card-type {			padding: .25rem .75rem;			background: white;			color: $color-2nd;			text-transform: uppercase;			font-size: .75rem;			font-weight: bold;			width: fit-content;			margin-bottom: .5rem;		}		.card-title {			margin-bottom: .25rem;		}		.desc {			display: flex;			flex-direction: row;			gap: 1rem;			align-items: center;			justify-content: space-between;						@include media-breakpoint-down(lg) {				flex-direction: column;				gap: 0;				height: calc(100% - 1rem);			}		}		.card-text {			flex-basis: 70%;						@include media-breakpoint-down(lg) {				flex-basis: 100%;			}		}		.btn {			background: $color-2nd;			color: white;			opacity: 1;			height: fit-content!important;			padding: .65rem .5rem;			font-size: .75rem;			font-weight: bold;			border: 0;						@include media-breakpoint-only(lg) {				font-size: .65rem;			}			@include media-breakpoint-down(lg) {				//position: relative;				opacity: 0!important;				width: 0!important;				height: 0!important;				//z-index: -1;			}		}	}		&.feature {		position: relative;		aspect-ratio: 310/500;				@include media-breakpoint-up(xl) {			//min-height: 500px;		}				.card-img-top {			aspect-ratio: 310/500!important;			min-height: auto!important;						@include media-breakpoint-up(xl) {				//min-height: 500px;
			}
			@include media-breakpoint-only(lg) {				//min-height: 400px;
			}
			@include media-breakpoint-down(lg) {				//height: 500px;
			}		}			.card-body {			position: absolute;			bottom: 0;			background: $color-2nd;
			background: linear-gradient(0deg, rgba( $color-2nd, .85) 50%, rgba( $color-2nd, 0) 100%);			color: white;			justify-content: flex-end;		}		.card-type {			background: $color-2nd;			color: white;			margin-bottom: .25rem;		}		.card-title {			margin-bottom: 0;			color: inherit;			font-weight: bold;		}		.desc {			flex-direction: column;			gap: 0;			color: inherit;			opacity: 0;			height: 0;		}		.card-text {			flex-basis: 100%;			color: inherit;			-webkit-line-clamp: 3;						> * {				color: inherit;			}		}		.btn {			//position: absolute;			opacity: 0;			height: 0!important;			padding: 0;		}				&:hover {			.card-body {				background: linear-gradient(0deg, rgba( $color-2nd, .9) 50%, rgba( $color-2nd, .1) 100%);				filter: saturate(1.5);							.card-title {					color: white;				}				.card-type {					background: white;					color: $color-main;				}				.desc {					opacity: 1;					height: 100%;				}				.card-text {					opacity: .75;				}			}		}	}}.card-article {	.card-body {		padding: 1rem 0 0;				.card-title {			color: $color-main;			font-weight: 800;			text-transform: uppercase;		}		.card-text {			opacity: .75;		}	}}form.searchForm {	display: flex;	flex-direction: row;	gap: 0;	align-items: center;	justify-content: center;	margin: 0 auto;		@include media-breakpoint-up(lg) {		width: 40vw;
	}	@include media-breakpoint-down(lg) {		width: 100%;
	}		border: 2px solid rgba(255, 255, 255, .35);	border-radius: .5rem;	padding: 0 1px;		.m-form {		&:nth-child(1) {			flex-basis: 100%;		}				input {			background-color: rgba(255, 255, 255, 0)!important;			border: 0!important;			color: white!important;		}		.btn, .btn:visited {			background: transparent;			border: 0!important;						&:hover, &:active {				//background: $color-2nd;				border-radius: 0 .25rem .25rem 0;				border: 0;				color: $color-2nd;				box-shadow: none;			}		}		.parsley-errors-list {			margin-left: 1.25rem;			margin-bottom: .25rem;			margin-top: 0;		}	}}.prune-titlepage {	position: relative;	background-position: center;		display: flex;	flex-direction: column;	justify-content: flex-end;		@include media-breakpoint-up(xl) {		min-height: 400px;
	}
	@include media-breakpoint-down(xl) {		min-height: 300px;
	}		&:before {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;		background: rgba( $color-main, .8);		z-index: 1;	}	&:after {		content: '';		position: absolute;		top: 0;		left: 0;		width: 100%;		height: 100%;
		background: linear-gradient(0deg,rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.2) 100%);		z-index: 1;	}		.container {		position: relative;		z-index: 2;		display: flex;		flex-direction: column;		justify-content: flex-end;	}		h1.title {		color: white;		text-transform: uppercase;		font-weight: 800;		font-size: 2rem;		width: 100%;		text-align: center;				@include media-breakpoint-down(xl) {			font-size: 1.25rem;
		}	}}.prune-block {	.container {		display: flex;		flex-direction: column;		gap: 3.5rem;				@include media-breakpoint-down(xl) {			gap: 2.75rem;		}	}		&.bgColor3 {		background: $color-3rd;	}	&.bgColor2 {		background: $color-2nd;	}	&.bgColor1 {		background: $color-main;	}}.recipe-related { //, .recipe-list	.list-block {		margin-top: 3rem;				@include media-breakpoint-down(lg) {			.card {				&:last-child {					flex-basis: 100%;										.card-img-top {						aspect-ratio: 16/11;					}				}			}
		}		}}.prune-grid {
	max-width: 100%;		@include media-breakpoint-up(lg) {		display: grid;
		grid-template-columns: repeat(3, 1fr);		column-gap: 1rem;		row-gap: 1rem;
	}	@include media-breakpoint-down(lg) {		display: flex!important;		flex-direction: column;		gap: 1rem;
	}		.column {		display: flex;		flex-direction: column;		gap: 1rem;		justify-content: space-between;		//align-items: stretch;	}		.item {		display: flex;		flex-direction: column;		gap: .25rem;		justify-content: center;				height: 100%;		border: 1px solid $color-main;		border-radius: .25rem;		padding: 2rem;				@include media-breakpoint-down(lg) {			border-radius: .625rem;
		}				.head {			display: flex;			flex-direction: row;			gap: 1rem;			align-items: center;		}		.title {			font-size: 1.25rem;			font-weight: bold;			text-transform: uppercase;			color: $color-main;			line-height: 1.5;		}		.icon {			width: 2.875rem;			aspect-ratio: 1/1;		}				.text, .tool-editor {			margin-top: 2rem;			//margin-bottom: 1rem;			color: black;						@include media-breakpoint-down(lg) {				text-align: justify;
			}						& > * {				color: inherit!important;				text-align: inherit!important;			}		}	}		@include media-breakpoint-down(lg) {		display: block;				&.slider {			.slick-track {				display: flex;				align-items: stretch;								.item {					height: auto;					margin: 0 .25rem;										.title {						text-align: center;					}										.tool-editor {						text-align: justify;												& > * {							text-align: inherit!important;						}					}				}			}			.slick-arrow {				&.btn-prev {					left: -.5rem;				}				&.btn-next {					right: -.5rem;				}			}		}	}}.contactForm {	position: relative;	overflow: hidden;		.container {		z-index: 2;		position: relative;	}		.heading {		margin-bottom: 2rem;				.text {			color: white;						* {				color: inherit;				text-align: inherit;				text-transform: uppercase;				font-weight: bold;								@include media-breakpoint-up(lg) {					font-size: 1.375rem;				}			}			@include media-breakpoint-down(lg) {				font-size: .875rem;				width: 80vw;				text-align: center;				margin: 0 auto;			}		}	}		.form-wrapper {		@include media-breakpoint-up(lg) {			display: grid;			grid-auto-flow: column;			//grid-template-columns: repeat(4, 1fr);			grid-auto-columns: auto 29% 29%;			grid-auto-rows: auto;			gap: 2rem;		}		@include media-breakpoint-down(lg) {			display: flex;			flex-direction: row;			justify-content: space-between;			gap: 1rem;			flex-wrap: wrap;		}				.card {			justify-self: stretch;			align-content: stretch;			position: relative;			border: 1px solid $color-4th;			border-radius: 1rem;			margin: 0;						.card-img-top {				display: none;								&[src^="assets/"] {					display: flex;				}			}						&:first-child {				background: rgba( $color-4th , .3);								@include media-breakpoint-up(lg) {					grid-area: 1 / 1 / 2 / 2;				}			}						&:nth-child(2) {				@include media-breakpoint-up(lg) {					grid-area: 2 / 1 / 3 / 2;										.card-body {						.card-title {							font-size: 1.5rem;							margin-bottom: 1rem;						}					}				}			}									&:nth-child(1), &:nth-child(2) {				@include media-breakpoint-down(lg) {					flex-basis: 47%;				}				@include media-breakpoint-only(md) {					flex-basis: 48.5%;
				}			}						&.form-block {				background: $color-main;				border: 0;				display: flex;				align-items: center;				justify-content: center;				gap: 1.5rem;								@include media-breakpoint-up(lg) {					grid-area: 1 / 2 / 3 / 4;					padding: 3rem;				}				@include media-breakpoint-down(lg) {					padding: 1.5rem;				}								.card-body {					position: relative;				}								.card-title {					color: white !important;					margin-bottom: 1rem!important;										@include media-breakpoint-down(lg) {						text-align: center;					}				}								form {					display: grid;					grid-template-columns: repeat(2, 1fr);					gap: 1.25rem;										.m-form {						justify-self: stretch;						grid-column: 1 / 3;												&:nth-child(1), &:nth-child(3) {							grid-column: 1 / 2;						}						&:nth-child(2), &:nth-child(4) {							grid-column: 2 / 3;						}					}					p.notice {						grid-column: 1 / 3;						color: white;						font-size: .75rem;					}					.btn, .btn:visited {						background: white;						color: $color-main;						border: 2px solid transparent;												&:hover, &:active {							background: none;							border: 2px solid white;							color: white;						}					}										[class*=parsley-errors-] {						color: var(--bs-body-error-color)!important;					}				}			}						.card-img-top {				@include media-breakpoint-down(lg) {					display: none;				}			}						.card-body {				width: 100%;				height: 100%;				display: flex;				flex-direction: column;				justify-content: center;				align-items: center;				color: inherit;				gap: 0;								@include media-breakpoint-up(lg) {					position: absolute;					padding: 1.5rem;				}				@include media-breakpoint-down(lg) {					padding: 1rem;					gap: .5rem;					justify-content: space-between;										.card-title, .card-text {						display: block;						overflow: visible;					}				}								.card-title {					font-size: 2rem;					color: inherit;					margin: 0;					text-transform: uppercase;					display: block;					text-align: center;										@include media-breakpoint-only(lg) {						font-size: 1.5rem;
					}					@include media-breakpoint-down(lg) {						//margin: 0 1rem;						font-size: 1rem;						text-align: center;					}				}				.card-text {					color: inherit;					text-align: center;					display: block;					text-align: center;										* {						color: inherit;						text-align: inherit;					}										@include media-breakpoint-up(lg) {						padding-left: 3rem;						padding-right: 3rem;						margin-bottom: 1rem;					}					@include media-breakpoint-only(lg) {						padding-left: 1rem;						padding-right: 1rem;
					}					@include media-breakpoint-down(lg) {						font-size: .75rem;					}									}				.btn, .btn:visited {					color: white;										@include media-breakpoint-down(md) {
						padding: .5rem 1rem;												@media (max-width: 375px) {							font-size: .65rem;						}					}					&:hover, &:active {						background: white;						color: $color-2nd;					}				}			}		}	}}.prune-static {	.tool-editor {		a, a:hover {			color: $color-2nd;		}				@include media-breakpoint-down(lg) {			//text-align: justify;						& > * {				text-align: inherit!important;			}		}	}}