/* Pages in partner */.prune-block:has(~ .prune-trade) {	@include media-breakpoint-down(lg) {		padding-bottom: 1rem;
	}}.prune-trade {	@include media-breakpoint-down(lg) {		padding: 1rem 0 2.5rem;
	}}.prune-handler {	padding-top: 0;		table {		width: 100%;				thead tr {			td {				font-weight: 700;				font-size: .75rem;				color: $color-main;								padding: .5rem 0;				border-bottom: 1px solid #E5E5E5;			}		}		thead tr, tbody tr {			td {				&:last-child {					text-align: right;										@include media-breakpoint-down(lg) {						display: none;					}				}								@include media-breakpoint-down(lg) {					&:nth-child(2) {						display: none;					}				}			}		}		tbody tr {			td {				padding: .75rem 0;				border-bottom: 1px solid #E5E5E5;								@include media-breakpoint-down(lg) {					padding: 1.25rem 0;				}								.name {					font-weight: 700;					font-size: 100%;					color: $color-main;				}				.addr, .phone, .email, .web {					display: flex;					flex-direction: row;					gap: .25rem;					align-items: baseline;										@include media-breakpoint-down(lg) {						gap: 1rem;						margin-bottom: .25rem;						max-width: 92vw;
					}										i {						font-size: 1rem;						color: $text-color;					}					a {						&:hover {							color: $color-2nd;						}					}										.tool-editor {						& > * {							margin: 0;						}					}				}				.addr {					@include media-breakpoint-up(lg) {						i.zmdi {							display: none;						}					}				}								.phone, .exportType {					@include media-breakpoint-up(lg) {						padding-top: 1rem;					}				}				.exportType {					font-size: .875rem;					font-weight: bold;					display: flex;										@include media-breakpoint-up(lg) {						flex-direction: column;						align-items: flex-end;					}					@include media-breakpoint-down(lg) {						flex-direction: row;						align-items: flex-start;						gap: .25rem;						flex-wrap: wrap;					}										span {						font-weight: inherit;												@include media-breakpoint-down(lg) {							&::after {								content: ' -';							}														&:last-child::after {								content: '';							}						}					}										@include media-breakpoint-down(lg) {						padding-top: 1rem;					}				}			}		}	}}