:root {	--bs-body-line-height: 1.625;}//FONT
$font: 'Mulish', var(--#{$variable-prefix}font-sans-serif);
$heading-font: 'Mulish', sans-serif;$heading-font-ultra: 'Mulish', sans-serif;

//TEXT
$font-size: 16px;$line-height: var(--bs-body-line-height);//FONT-WEIGHT$normal: normal;$bold: bold;$extra: 900;

//TEXT COLOR$text-color: #212529;//COLOR BRAND$bg-default: #000;$color-main: #542A45;$color-2nd: #CE9F54;$color-3rd: #F1ECEF;//more$color-4th: #02BCE3;$gray-light: #EAEAEA;$gray: #999;//ANIMATION$blend-mode : lighten;///*@media (min-width: 576px) {
