/* Hero Banner Config */.hero-banner {	position: relative;	padding: 0;		.slider {		.slick-dots {			margin-top: 0!important;			position: absolute;						@include media-breakpoint-up(lg) {				//display: none!important;				bottom: 6rem;			}			@include media-breakpoint-down(lg) {				bottom: 2rem;			}						li button {				background-color: rgba(255, 255, 255, .5)			}						li.slick-active button, li:hover button {				background-color: $color-2nd;			}		}				.slick-arrow {			@include media-breakpoint-down(lg) {				display: none!important;			}		}	}		.item {		position: relative;		height: 100vh;				&:before {			content: '';			position: absolute;			width: 100%;			height: 100%;			background: rgba($color-main, .6);
			//background: linear-gradient(0deg, rgba(10,21,54,1) 0%, rgba(10,21,54,0) 100%);			//bottom: 0;			top: 0;			left: 0;			z-index: 1;						/*@include media-breakpoint-up(lg) {				//height: 9.375rem;				height: 100vh;			}			@include media-breakpoint-down(lg) {				//height: 50px;				height: 100vh;			}*/		}				img {			width: 100vw;			height: 100vh;			object-fit: cover;		}				i {			position: absolute;			top: 50%;			left: 50%;			transform: translate(-50%, -50%);			color: red;			//color: $color-main;			font-size: 4rem;			z-index: 2;		}				.banner-content {			position: absolute;			top: 50%;			left: 50%;			transform: translate(-50%, -50%);			color: white;			//font-size: 1rem;			z-index: 2;						text-align: center;						@include media-breakpoint-down(xl) {				width: 100%;				padding: 1rem;			}						* {				font-size: 1.625rem;				color: inherit;			}						.btn, .btn:visited {				font-size: .875rem;				text-transform: uppercase;				//font-weight: bold;				padding: .92rem 1.5rem;				border: 2px solid white;				background: transparent;				color: inherit;								&:hover, &:active {					background: white;					color: $color-main;				}			}					}	}}