.article.newsHot {	@include media-breakpoint-only(lg) {		.container {			padding: 3rem 3rem 2.5rem;		}	}		.heading {		flex-direction: column;		align-items: center;		justify-content: center;		max-width: 960px;		gap: 1rem;		margin: 0 auto 1.5rem;				* {			text-align: center;		}	}		.list-block {		@include media-breakpoint-down(xl) {			&.slider {				.slick-arrow {					display: none!important;				}			}		}	}}.article.list {	//padding-top: 0;		@include media-breakpoint-down(lg) {		padding-bottom: 3.75rem;	}		.heading {		align-items: center;		margin-bottom: 1rem;				.nav-tabs {			margin-bottom: 2rem;			justify-content: center;						@include media-breakpoint-down(lg) {				margin-bottom: 1rem;			}		}				.title + .nav-tabs {			margin-top: 1rem;		}	}}.article.topic {	@include media-breakpoint-up(xl) {		padding-top: 8rem;	}	.heading {		justify-content: flex-start;		width: 100%;				.title, .nav-tabs {			max-width: 100%;			width: 100%;		}				.title {			text-align: left;						@include media-breakpoint-up(xl) {				font-size: 2rem;			}		}				.nav-tabs {			justify-content: flex-start;			margin-bottom: 1rem;						.nav-item .nav-link {				padding: .5rem 1rem;				font-size: .75rem;			}		}	}}.article.related-box {	padding-bottom: 3.75rem;	background: $color-3rd;		@include media-breakpoint-down(xl) {		padding-bottom: 2rem;	}		.heading {		margin: 0 auto 1.5rem;	}		.list-block {		display: flex;		flex-direction: row;		gap: 1.5rem;		padding: 2rem 0 1rem;		justify-content: space-between;				@include media-breakpoint-down(xl) {			row-gap: 0!important;			padding-bottom: 0;			margin-bottom: 0!important;		}		@include media-breakpoint-down(md) {			flex-wrap: wrap;			gap: 1rem;		}				.card {			margin: 0;						@include media-breakpoint-down(md) {				flex-basis: 47%;								&:first-child {					flex-basis: 100%;				}			}						&:hover {				.card-body .card-title {					color: $color-2nd;				}			}						.card-img-top {				border-radius: 0;			}						.card-body {				.card-text {					opacity: .75;										@include media-breakpoint-down(lg) {						//display: none;					}				}				/*.btn {					display: none;				}*/			}		}	}}